<template>
  <div>
    <el-row :style="{ height: '40px', marginBottom: '0' }">
      <el-col :span="6">
        <p
          class="waplus-title"
          :style="{
            fontStyle: 'normal',
            fontSize: '18px',
            fontWeight: 'bold',
            color: '#37B64A',
            textAlign: 'left',
            backgroundColor: '#F9F9F9'
          }"
        >
          {{ waPlusSenderText }}
        </p>
      </el-col>
      <el-col :span="8">
        <span v-if="permissionText !== 'Pro'" class="activateProLink" @click="clickActivateCode"
          >Activate Code
        </span>
        <span v-if="permissionText === 'Pro'">&nbsp;</span>
      </el-col>
      <el-col :span="6">
        <span v-if="permissionText !== 'Pro'" class="activateProLink" @click="openPricing"
          >Upgrade!!!
        </span>
        <span v-if="permissionText === 'Pro'">&nbsp;</span>
      </el-col>
      <el-col :span="6" class="topText">
        <div>
          <div>
            <div
              :class="['permission', permissionText === 'Pro' ? 'pro' : 'trial']"
              @click="openPricing"
              v-text="permissionText"
            ></div>
          </div>
        </div>
      </el-col>
      <el-col :span="2">
        <div class="profileInfoIcon" @click="openUserProfile()">
          <svg
            t="1673427269872"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="3496"
            width="20"
            height="20"
          >
            <path
              d="M896 256H128V128h768v128z m0 192H128v128h768V448z m0 320H128v128h768V768z"
              p-id="3497"
              fill="#606266"
            ></path>
          </svg>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'TopInfo',
  data() {
    return {
      waPlusSenderText: 'Bulk Sender',
      numberText: 'Number: + null',
      browserType: 'Chrome'
    }
  },
  props: {
    permissionText: String
  },
  created: async function () {
    let _this = this
    chrome.storage.local.get(['userPhoneNum'], (res) => {
      if (res.userPhoneNum) {
        _this.numberText = 'Number: +' + res.userPhoneNum
      }
    })
  },
  methods: {
    openPricing() {
      this.$emit('openPricing', 'top')
    },
    clickActivateCode() {
      // 
      this.$sendLog(908304)
      this.$emit('openActiveCodePopup')
    },
    openUserProfile() {
      this.$emit('openUserProfile')
    }
  }
}
</script>

