<template>
  <div class="btn-msg">
    <el-dialog
      :visible.sync="buttonMsgDialogVisible1"
      width="90%"
      style="margin-top: 10px"
      :before-close="handleClose"
    >
      <div slot="title">
        <div class="btn-msg-header">
          <img src="images/rocket.png" alt="" />
          <h1>{{ $t('btn_msg_remind_title') }}</h1>
        </div>
      </div>
      <div class="btn-msg-body">
        <p class="margin-top-bottom">
          <!-- JSON  part1-->
          {{ $t('btn_msg_remind_1')
          }}<span class="color-red-weight-big">{{ $t('btn_msg_remind_2') }}</span
          >{{ $t('btn_msg_remind_3') }}
        </p>
        <el-carousel
          :interval="5000"
          arrow="always"
          height="250px"
          style="width: 231px; margin: 0 auto"
        >
          <el-carousel-item v-for="item in imagesList" :key="item.id">
            <img :src="item.url" alt="" style="width: 231px; margin: 0 auto; display: block" />
          </el-carousel-item>
        </el-carousel>
        <p class="margin-top-bottom">{{ $t('btn_msg_remind_5') }}</p>
        <p>
          <span id="btn_msg_remind_part4" v-html="btnMsgPart4"></span>
          <span class="color-red-weight-big">{{ $t('btn_msg_remind_6') }}</span
          >{{ $t('btn_msg_remind_7') }}
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="sendButtonMsgToMyself">{{
          $t('but_msg_remind_button')
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { POP_TO_CONTENT_BUTTON_MESSAGE } from '@/service/constants.js'
export default {
  props: ['showBtnMsgImg', 'buttonMsgDialogVisible'],
  data() {
    return {
      btnMsgPart4: this.$t('btn_msg_remind_4'),
      // get
      imagesList: [
        {
          id: '3',
          url: 'images/3.png'
        },
        {
          id: '2',
          url: 'images/2.png'
        },
        {
          id: '1',
          url: 'images/1.png'
        }
      ],
      // data
      buttonMsgData: {
        // API
        contactno: '',
        content: "Welcome aboard! Let's explore the new features together~",
        button: [
          {
            id: 'another id 01',
            text: 'Sure '
          },
          {
            id: 'another id 11',
            text: ' OK '
          }
        ]
      },
      // TODO Msg
      buttonMsgDialogVisible1: false
    }
  },
  created() {},
  methods: {
    sendButtonMsgToMyself() {
      let _This = this
      chrome.storage.local.get(['permissionInfo', 'userPhoneNum'], async function (res) {
        _This.buttonMsgData.contactno = `${res.userPhoneNum || '861779214163'}@c.us`
        // util
        const tab = await chrome.tabs.query({ active: true, currentWindow: true })
        // tab
        _This.$bridge.sendMessage(
          POP_TO_CONTENT_BUTTON_MESSAGE,
          { buttonMsgData: _This.buttonMsgData, userPhoneNum: res.userPhoneNum },
          `content-script@${tab[0].id}`
        )
        _This.$emit('update:buttonMsgDialogVisible', false)
        chrome.storage.local.set({
          BulkSender_btnMsg: false
        })
        _This.$emit('update:showBtnMsgImg', true)
      })
    },
    handleClose() {
      this.sendButtonMsgToMyself()
    }
  }
}
</script>

