<template>
  <div class="strengthen-box">
    <el-card
      class="box-card strengthen-card"
      shadow="always"
      :body-style="{ padding: '20px 16px 0 16px' }"
    >
      <div class="strengthen-title">{{ $t('bulk_privacy.title') }}</div>
      <div class="strengthen-content">
        <div class="strengthen-content-item">
          <el-checkbox v-model="messagesPreview" @change="messagesPreviewChange">{{
            $t('bulk_privacy.content1')
          }}</el-checkbox>
        </div>
        <div class="strengthen-content-item">
          <el-checkbox v-model="name" @change="nameChange">{{
            $t('bulk_privacy.content2')
          }}</el-checkbox>
        </div>
        <div class="strengthen-content-item">
          <el-checkbox v-model="profilePic" @change="profilePicChange">{{
            $t('bulk_privacy.content3')
          }}</el-checkbox>
        </div>
        <div class="strengthen-content-item">
          <el-checkbox v-model="messages" @change="messagesChange">{{
            $t('bulk_privacy.content4')
          }}</el-checkbox>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Strengthen',
  data() {
    return {
      messagesPreview: false,
      name: false,
      profilePic: false,
      messages: false
    }
  },
  mounted() {},
  methods: {
    messagesPreviewChange() {
      chrome.storage.sync.set({ messagesPreview: this.messagesPreview })
      this.refreshScript()
      this.$sendLog(902401, {
        changeType: 'messagesPreview'
      })
    },
    profilePicChange() {
      chrome.storage.sync.set({ profilePic: this.profilePic })
      this.refreshScript()
      this.$sendLog(902401, {
        changeType: 'profilePic'
      })
    },
    nameChange() {
      chrome.storage.sync.set({ name: this.name })
      this.refreshScript()
      this.$sendLog(902401, {
        changeType: 'name'
      })
    },
    messagesChange() {
      chrome.storage.sync.set({ messages: this.messages })
      this.refreshScript()
      this.$sendLog(902401, {
        changeType: 'messages'
      })
    },

    /**
     * @description: whatsapp Dom
     * @return {*}
     */
    refreshScript() {
      chrome.tabs.query({ url: '*://*.whatsapp.com/*' }, function (tabs) {
        if (tabs.length !== 0)
          tabs.forEach(function (tab) {
            chrome.scripting.executeScript({
              target: { tabId: tab.id },
              files: ['js/load.js']
            })
          })
      })
    },
    /**
     * @description: storage
     * @return {*}
     */
    async setCurrentStateInPop() {
      const data = await chrome.storage.sync.get([
        'messages',
        'messagesPreview',
        'profilePic',
        'name'
      ])

      this.messages = data.messages
      this.messagesPreview = data.messagesPreview
      this.profilePic = data.profilePic
      this.name = data.name
    }
  },
  created() {
    this.setCurrentStateInPop()
  }
}
</script>

<style scoped>
.strengthen-box {
  margin-top: 10px;
}

.strengthen-title {
  margin-bottom: 24px;
  color: rgba(16, 16, 16, 1);
  font-size: 20px;
}

.strengthen-content {
  padding-left: 16px;
}

.strengthen-content-item {
  margin-bottom: 24px;
  font-size: 18px;
}

.strengthen-content-item >>> .el-checkbox__input {
  line-height: normal;
}

.strengthen-content-item >>> .el-checkbox__input.is-focus .el-checkbox__inner {
  border-color: #36b64a;
}

.strengthen-content-item >>> .el-checkbox__inner:hover {
  border-color: #36b64a;
}

.strengthen-content-item >>> .el-checkbox__input.is-checked + .el-checkbox__label {
  color: #36b64a;
}

.strengthen-content-item >>> .el-checkbox__input.is-checked .el-checkbox__inner,
.strengthen-content-item >>> .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #36b64a;
  border-color: #36b64a;
}
</style>
