<template>
  <div class="more-tools">
    <ul>
      <li v-for="item in moreToolsData" :key="item.id">
        <img class="tools-img" :src="item.iconUrl" alt="" />
        <a href="javascript:;" class="tools-link" @click="linkToUrl(item.linkUrl)"
          ><span>{{ item.pluginDescriptor }}</span>
          <template>
            <el-tag
              v-for="(tag, index) in item.tags"
              :key="index"
              :type="tag.type"
              effect="plain"
              size="small"
            >
              {{ tag.text }}
            </el-tag>
          </template>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import moreToolsDataJson from '../../../public/datajson/moreToolsDataJson.json'
import { POP_TO_BACKGROUND_URL } from '@/service/constants.js'
export default {
  data() {
    return {
      moreToolsData: []
    }
  },
  async created() {
    let { zbaseConfig } = await chrome.storage.local.get(['zbaseConfig'])
    console.log(zbaseConfig)
    // 
    if (zbaseConfig) {
      const data = zbaseConfig.data.config.filter((item) => {
        return item.name === 'moreTools'
      })
      if (data && data.length != 0 && data[0].params) {
        this.moreToolsData = data[0].params
      } else {
        this.moreToolsData = moreToolsDataJson
      }
      this.sortData()
    } else {
      // 
      this.moreToolsData = moreToolsDataJson
      this.sortData()
    }
  },
  methods: {
    // id
    sortData() {
      this.moreToolsData.sort((item1, item2) => {
        if (item1.id > item2.id) {
          return 1
        } else {
          return -1
        }
      })
    },
    /**
     * BGBg
     * @param {*} url 
     */
    linkToUrl(url) {
      this.$bridge.sendMessage(POP_TO_BACKGROUND_URL, { url: url }, 'background')
      // msg
      // chrome.runtime.sendMessage({ type: 'url', data: url }, function () {})
    }
  }
}
</script>

