            {{ $t('bulk_group_contacts_download') }}
          </template>
          <div class="grouptools-box">
            <div style="font-size: 18px; line-height: 30px; margin-top: 12px">
              {{ $t('tool_choose_file') }}
            </div>
            <div class="radioBox">
              <el-radio-group
                style="padding-left: 10px"
                v-model="radiosExportType"
