                class="groupToolSelected"
                @visible-change="handleGroupChangeLog"
                :placeholder="this.$t('select_joined_export_t')"
                :no-data-text="this.$t('no_data_t')"
                style="width: 510px; padding: 10px 0 10px 10px"
                v-model="valueGroup"
