<script>
import { getAllPhoneNumberList } from '@/utils/phone-number-util'
import { EXTENSION_NAME, WAPLUS_REMOVE_FLAG } from '@/utils/extension-util'
import { loggerFeatureId } from '@/utils/log-util'
import Accountdetection from './components/Accountdetection.vue'
import AutoReply from './components/AutoReply.vue'
import AutoReplySvg from './components/AutoReplySvg.vue'
import VerificationSvg from './components/VerificationSvg.vue'

export default {
  name: 'GroupTools',
  props: [
    'isBusiness',
    'chooseWarning',
    'beRemovedWarning',
    'labelOptions',
    'groupOptions',
    'groupsMap',
    'permissionText'
  ],
  components: {
    Accountdetection,
    AutoReply,
    AutoReplySvg,
    VerificationSvg
  },
  data() {
    return {
      valueGroup: '',
      valueLabel: '',
      excludeGroupAdminsValue: true,
      extensionName: EXTENSION_NAME,
      waplus_remove_flag: WAPLUS_REMOVE_FLAG,
      radiosChat: 'radioChatAll',
      radiosExportType: 'xlsx',
      firstLoadValue: false,
      browserType: 'Chrome',
      title: chrome.runtime.getManifest().name,
      userProfile: 'User Profile: +',
      showLabelWarn: false
    }
  },
  created() {
    const _this = this
    chrome.storage.local.get(
      [
        'groupOptions',
        'groupsMap',
        'valueGroup',
        'excludeGroupAdminsValue',
        'radiosChat',
        'radiosExportType'
      ],
      (res) => {
        _this.groupOptions = res.groupOptions !== undefined ? res.groupOptions : []
        _this.groupsMap = res.groupsMap || {}
        _this.valueGroup = res.valueGroup || ''
        _this.excludeGroupAdminsValue =
          res.excludeGroupAdminsValue !== undefined ? res.excludeGroupAdminsValue : true
        _this.radiosChat = res.radiosChat !== undefined ? res.radiosChat : 'radioChatAll'
        _this.radiosExportType = res.radiosExportType !== undefined ? res.radiosExportType : 'xlsx'
      }
    )
  },

  methods: {
    openPricing() {
      this.$sendLog(908302, { s_feature_id: loggerFeatureId.GROUP_EXPORT })
      this.$emit('openPricing')
    },
    handleGroupChange(value) {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      let group = this.groupsMap[this.valueGroup]
      chrome.storage.local.set({ valueGroup: this.valueGroup })
      let phoneNumberList = getAllPhoneNumberList(group)
      this.$parent.messageBoxClose()
      if (phoneNumberList.length === 0) {
        console.log('be removed out the group')
      }
    },
    handleGroupChangeLog(value) {
      if (value) {
        this.$sendLog(903205)
      }
      if (!this.groupOptions || this.groupOptions.length === 0) {
        this.$sendLog(903220)
      }
    },
    handleLabelChangeLog(value) {
      if (value) {
        this.$sendLog(903207)
      }
      if (!this.labelOptions || this.labelOptions.length === 0) {
        this.$sendLog(903221)
      }
    },
    handleLabelChange(label) {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      chrome.storage.local.set({ valueLabel: this.valueLabel })
      this.$parent.messageBoxClose()
    },
    exportClickGroup() {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      this.$sendLog(903201, {
        s_status:
          this.radiosExportType === 'xlxs'
            ? 1
            : this.radiosExportType === 'csv'
            ? 2
            : this.radiosExportType === 'vcf'
            ? 3
            : ''
      })
      const sendData = {
        exportType: this.radiosExportType,
        exportFrom: 'group',
        valueGroup: this.valueGroup,
        excludeGroupAdminsValue: this.excludeGroupAdminsValue
      }
      this.$parent.sendDataPopToContent(sendData)
    },
    // labels
    exportClickLabels() {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      // 
      if (!this.isBusiness) {
        this.showLabelWarn = true
        let remindBorder = document.querySelector('.labelSelect .el-input--suffix .el-input__inner')
        remindBorder.style.borderColor = 'rgba(255, 5, 5, 100)'
        return
      }
      this.$sendLog(903208, {
        s_status:
          this.radiosExportType === 'xlxs'
            ? 1
            : this.radiosExportType === 'csv'
            ? 2
            : this.radiosExportType === 'vcf'
            ? 3
            : ''
      })
      const sendData = {
        exportType: this.radiosExportType,
        exportFrom: 'label',
        valueLabel: this.valueLabel
      }
      this.$parent.sendDataPopToContent(sendData)
    },

    //export click
    async exportClickChat() {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      this.$sendLog(903206, {
        s_chat_type:
          this.radiosChat === 'radioChatAll' ? 1 : this.radiosChat === 'radioChatUnsaved' ? 2 : '',
        s_status:
          this.radiosExportType === 'xlxs'
            ? 1
            : this.radiosExportType === 'csv'
            ? 2
            : this.radiosExportType === 'vcf'
            ? 3
            : ''
      })
      const sendData = {
        exportFrom: 'chat',
        exportGroup: this.radiosChat,
        exportType: this.radiosExportType
      }
      this.$parent.sendDataPopToContent(sendData)
    },
    // popcontent
    handleRadiosChatChange(value) {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      chrome.storage.local.set({ radiosChat: this.radiosChat })
    },
    handleRadiosExportTypeChange() {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      chrome.storage.local.set({ radiosExportType: this.radiosExportType })
    },
    handleExcludeAdminValueChange(value) {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.GROUP_EXPORT
        })
        return
      }
      this.$sendLog(903202)
      chrome.storage.local.set({ excludeGroupAdminsValue: value })
    },
    handleCheckUserPermission(featureId) {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: featureId
        })
        return false
      }
      return true
    }
  }
}
</script>

