<script>
import { INVITE_COMMENT_URL } from '@/utils/common/config'
import { getContinueStopFlag } from '@/utils/send-util'
import { deleteList } from '@/utils/common/common-util'
import { WAPLUS_REMOVE_FLAG } from '@/utils/extension-util'
import { loggerFeatureId } from '@/utils/log-util'

export default {
  name: 'Statistics',
  props: [
    'disabledSendingFlag',
    'isShowContinueBtn',
    'countAll',
    'countFail',
    'countSuccess',
    'waitSeconds',
    'countTotal',
    'deduplicated',
    'permissionText',
    'setShowStatistics'
  ],
  data() {
    return {
      retryLoading: false,
      currentSendingProgressText: this.$t('current_sending_t'),
      progressColor: '#35B64A',
      percentage: 0,
      nextMessageWillBeSentAfterText: this.$t('please_do_t'),
      groupIntervalText: 'Group Interval',
      groupIntervalNoticeText: this.$t('every_t'),
      sendResultsText: this.$t('send_results_t'),
      totalText: this.$t('total_t'),
      deduplicatedText: this.$t('deduplicated_t'),
      sentSuccessfullyText: this.$t('sent_successfully_t'),
      failedToSendText: this.$t('failed_to_t'),
      loadTimes: 0,
      phoneNumList: [],
      inviteCommentUrl: true,
      waplus_remove_flag: WAPLUS_REMOVE_FLAG
    }
  },
  created: async function () {
    const _This = this
    chrome.storage.local.get(['percentage', 'loadTimes'], (res) => {
      _This.percentage = res.percentage
      _This.loadTimes = res.loadTimes !== undefined ? res.loadTimes : 1
    })
  },
  computed: {
    pauseDisabled() {
      return this.countAll === 0 || this.retryLoading
    },
    isDisabledContinueBtn() {
      let isDisable = true
      if (this.countFail + this.countSuccess < this.countAll) {
        isDisable = false
        return isDisable
      }
      return isDisable
    }
  },
  methods: {
    handleBack() {
      this.setShowStatistics && this.setShowStatistics(false)
    },
    openPricing() {
      this.$sendLog(908302, { s_feature_id: loggerFeatureId.STATISTICS })
      this.$emit('openPricing')
    },
    openCommentWindow() {
      window.open(INVITE_COMMENT_URL)
    },
    format(percentage) {
      let result = this.countSuccess + this.countFail + '/' + this.deduplicated
      // let countAll = this.countAll === 0 ? 1 : this.countAll;
      this.percentage = ((this.countSuccess + this.countFail) / this.deduplicated) * 100
      return result
    },
    continueClick() {
      this.$sendLog(904203)
      if (this.waitSeconds !== 0 && this.loadTimes !== 1) {
        return
      }
      if (this.countFail + this.countSuccess === this.countAll) {
        return
      }
      this.loadTimes = this.loadTimes + 1
      chrome.storage.local.set({ loadTimes: this.loadTimes })
      let _This = this
      let dailySendNums = this.$parent.dailySendNums
      // const {isReachSendLimit, showConfirmTitle} =
      //     this.$checkDailySendNums(dailySendNums);
      // if (isReachSendLimit) {
      //   this.$confirm(showConfirmTitle, "", {
      //     confirmButtonText: "Continue",
      //     cancelButtonText: "Cancel",
      //     type: "warning",
      //     showClose: false,
      //     customClass: "daily-msg-send-warning",
      //     dangerouslyUseHTMLString: true
      //   }).then(() => {
      //     chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
      //       chrome.tabs.sendMessage(tabs[0].id, {limit_continue:true});
      //     });
      //   });
      //   setTimeout(function () {
      //     document.getElementsByClassName('limit_popup')[0].addEventListener('click',function () {
      //       window.open(LIMIT_POPUP_CRM)
      //     })
      //   },200)
      //   return;
      // }
      this.$parent.updateIsShowContinueBtn(false)
      this.$parent.updateDisabledSendingFlag(true)
      chrome.storage.local.get(['continueSendData', 'audio'], function (res) {
        let continueSendData = res.continueSendData //contentjs
        if (continueSendData !== undefined) {
          let { stopFlag, stopFlagSimple } = getContinueStopFlag(continueSendData.sendMessageType)
          chrome.storage.local.set({
            stopFlag: stopFlag,
            stopFlagSimple: stopFlagSimple,
            waitSeconds: 0
          })
          _This.$parent.sendDataPopToContent({
            continueSendData: continueSendData,
            dailySendNums: dailySendNums,
            audio: res.audio || {}
          })
        }
      })
    },
    sendStopClick() {
      this.$sendLog(904202)
      let _This = this
      this.loadTimes = this.loadTimes + 1
      chrome.storage.local.set({ loadTimes: this.loadTimes })
      this.loadingMsgBox('Pausing, please wait.')
      if (this.waitSeconds !== 0) {
        setTimeout(() => {
          _This.$msgbox.close()
        }, (this.waitSeconds || 0) * 1000 + 500)
      }
      let phoneNum = ''
      let uploadExcel
      if (this.$parent.sendMessageType === 'simple') {
        phoneNum = this.phoneNumList.length > 0 ? this.phoneNumList.join(',') : ''
        uploadExcel = this.uploadExcelValue
      }
      if (this.$parent.sendMessageType === 'pro') {
        phoneNum = this.phoneNumList.join(',')
      }
      chrome.storage.local.set({ stopFlag: true, stopFlagSimple: true })
      let sendData = {
        stopFlag: true,
        stopFlagSimple: true,
        sendMessageType: this.$parent.sendMessageType,
        phoneNum: phoneNum,
        uploadExcel: uploadExcel
      }
      this.$parent.sendDataPopToContent(sendData)
      this.$parent.updateIsShowContinueBtn(this.countSuccess + this.countFail !== this.countAll)
    },
    loadingMsgBox(message) {
      const h = this.$createElement
      this.$confirm('', '', {
        customClass: 'emptyContent deleteConfirmHeader',
        showConfirmButton: false,
        showCancelButton: false,
        showClose: false,
        closeOnPressEscape: false,
        closeOnClickModal: false,
        closeOnHashChange: false,
        center: true,
        message: h('div', null, [
          h('div', { style: { height: '24px' } }, [
            h('div', {
              attrs: { class: 'el-icon-warning loading-green-icon' },
              style: { width: '24px', height: '24px', fontSize: '24px' }
            }),
            h('span', {
              domProps: { innerHTML: message },
              style: {
                color: 'rgba(48, 49, 51, 100)}',
                fontSize: '18px',
                margin: 'auto auto 0 18px'
              }
            })
          ]),
          h('div', {
            attrs: { class: 'el-icon-loading loading-green-icon' },
            style: { fontSize: '50px', margin: '53px auto 30px auto' }
          })
        ])
      })
    },
    exportResultsClick() {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.STATISTICS
        })
        return
      }
      this.$sendLog(904204)
      let _This = this
      chrome.storage.local.get(
        ['phoneNumList', 'phoneNumSuccessList', 'phoneNumFailList', 'repeatList'],
        function (res) {
          let resultList = []
          let allList = res.phoneNumList ? res.phoneNumList : []
          let successList = res.phoneNumSuccessList ? res.phoneNumSuccessList : []
          let failList = res.phoneNumFailList ? res.phoneNumFailList : []
          let repeatList = res.repeatList ? res.repeatList : []
          let noSendList = deleteList(allList, successList)
          noSendList = deleteList(noSendList, repeatList)
          let sLength = successList.length
          let fLength = failList.length
          let nLength = noSendList.length
          let rLength = repeatList.length
          let maxLength = Math.max(sLength, fLength, nLength, rLength)
          for (let i = 0; i < maxLength; i++) {
            let tmp = {
              'Sent successfully': successList[i] ? successList[i] : '',
              '': '',
              'Failed to send': failList[i] ? failList[i] : '',
              ' ': '',
              'Not sent yet': noSendList[i] ? noSendList[i] : '',
              '  ': '',
              'Duplicated number': repeatList[i] ? repeatList[i] : ''
            }
            resultList.push(tmp)
          }
          _This.$parent.fnExport(resultList)
        }
      )
    },
    async retryFailedClick() {
      this.$sendLog(904205)
      if (this.permissionText === 'Free') {
        let dailySendNums = this.$parent.dailySendNums
        const { isReachSendLimit, showConfirmTitle } = this.$checkDailySendNums(dailySendNums)
        if (isReachSendLimit) {
          // , todo 
          this.$emit('openPayPermissionPopup', {
            featureId: loggerFeatureId.STATISTICS,
            s_message: 'retry failed'
          })
          return
        }
      }
      let _This = this
      let waitSendNum = this.countAll - this.countSuccess
      if (waitSendNum + this.countFail === 0) {
        this.$confirm('', 'All messages were sent successfully.', {
          customClass: 'emptyContent',
          showConfirmButton: false,
          showCancelButton: false,
          center: true,
          type: 'warning'
        })
        return
      }

      // if (isReachSendLimit) {
      //   this.$confirm(showConfirmTitle, "", {
      //     confirmButtonText: "Continue",
      //     cancelButtonText: "Cancel",
      //     type: "warning",
      //     showClose: false,
      //     customClass: "daily-msg-send-warning",
      //     dangerouslyUseHTMLString: true
      //   }).then(() => {
      //     chrome.tabs.query({active: true, currentWindow: true}, function (tabs) {
      //       chrome.tabs.sendMessage(tabs[0].id, {limit_continue:true});
      //     });
      //   });
      //   setTimeout(function () {
      //     document.getElementsByClassName('limit_popup')[0].addEventListener('click',function () {
      //       window.open(LIMIT_POPUP_CRM)
      //     })
      //   },200)
      //   return;
      // }

      if (this.$parent.sendMessageStatisticsType === 'pro') {
        chrome.storage.local.set({
          retryFailed: true,
          stopFlag: false,
          stopFlagSimple: 'disabled'
        })
      }
      if (this.$parent.sendMessageStatisticsType === 'simple') {
        chrome.storage.local.set({
          retryFailed: true,
          stopFlag: 'disabled',
          stopFlagSimple: false
        })
      }
      this.$parent.updateIsShowContinueBtn(false)
      this.retryLoading = true
      await _This.$parent.sleep(1, 1, 1000)
      let sendData = { reload: true }
      chrome.storage.local.set({
        stopFlag: false,
        stopFlagSimple: false
      })
      this.$parent.sendDataPopToContent(sendData)
    }
  }
}
</script>

