<script>
import config from '../../../package.json'
import { getBrowser } from '@/utils/browser-util'
import {
  POP_TO_BACKGROUND_GET_PERMISSION,
  POP_TO_BACKGROUND_GET_CUSTOMERSERVICE
} from '@/service/constants.js'
export default {
  props: {
    sendType: {
      type: String,
      default: ''
    },
    groupOptions: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  name: 'CommonBottomHref',
  computed: {
    curLang() {
      let basic = ['en', 'zh', 'pt', 'es', 'ar']
      return basic.includes(this.$i18n.locale) ? this.$i18n.locale : 'en'
    }
  },
  data() {
    return {
      browserType: 'Chrome',
      versionText: '1.0.0',
      showDesktop: this.$i18n.locale === 'zh-CN',
      isShowFreeGift: true
    }
  },
  created: async function () {
    let _This = this
    this.browserType = getBrowser()['browser']
    this.versionText = config.version
    chrome.storage.local.get(['isShowFreeGift'], (res) => {
      _This.isShowFreeGift = res?.isShowFreeGift
    })
  },
  methods: {
    /**
     * @description: 
     * @param {*} code
     * @return {*}
     */
    handleI18nChange(code) {
      this.$i18n.locale = code
      chrome.storage.local.set({ locale: code })
    },
    /**
     * @description: 
     * @return {*}
     */
    async handleFreeGiftClick() {
      this.$sendLog(900017)
      const tab = await chrome.tabs.query({ active: true, currentWindow: true })
      // tab
      this.$bridge?.sendMessage(
        'POP_TO_CONTENT_SET_COMMENT_VISIBLE',
        {},
        `content-script@${tab[0].id}`
      )
    },
    goDesktop() {
      window.open('https://waplus.io/waplus-to-wadesk?utm_source=wapi')
    },
    /**
     * @description: WAPlusWA
     * @return {*}
     */
    async contactCustomer() {
      // 
      if (this.isContact == true) {
        return
      }
      this.isContact = true
      const res = await this.$bridge.sendMessage(POP_TO_BACKGROUND_GET_PERMISSION, {}, 'background')
      this.isContact = false
      if (!res) return
    },
    howToUse() {
      this.$sendLog(908310)
      // window.open('https://scrm-global.zingfront.com/sender/wapi_sender/how_to_use.html')
      window.open('https://wawebsender.com/sender-how-to-use')
    },
    faq() {
      window.open('https://wawebsender.com/sender-faq')
    },
    openUrl(url) {
      window.open(url)
    }
  }
}
</script>

