<template>
  <div>
    <el-dialog
      :visible.sync="commentsDialogVisible"
      width="90%"
      style="margin-top: 10px"
      title="WA Bulk Message Sender"
      :before-close="handleClose"
      center
    >
      <div class="m-comments-body">
        <p>Rate us 5 stars if you're loving the extension!</p>
        <p>Your reviews help new users.</p>
        <div class="m-comments-block">
          <el-rate width="200" v-model="commentsValue"></el-rate>
        </div>
        <div class="m-comments-text" v-show="showCommentsText">
          <el-input
            show-word-limit
            type="textarea"
            :rows="4"
            placeholder="Please enter content"
            clearable
            v-model="commentsText"
          >
          </el-input>
        </div>
        <el-button type="success" v-show="showCommentsText" @click="submitComments">
          Submit
        </el-button>
        <el-button type="success" v-show="showGoogleBtn" @click="openChromeUrl">
          Comment Now
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['googleUrl', 'logId'],
  data() {
    return {
      commentsValue: null,
      commentsDialogVisible: false,
      showCommentsText: false,
      commentsText: '',
      showGoogleBtn: false
    }
  },
  watch: {
    commentsValue(newVla, oldVal) {
      if (newVla === 5) {
        this.showCommentsText = false
        this.showGoogleBtn = true
      } else if (newVla === 0) {
        this.showCommentsText = false
        this.showGoogleBtn = false
      } else {
        this.showCommentsText = true
        this.showGoogleBtn = false
      }
    }
  },
  mounted() {
    this.commentsInit()
  },
  methods: {
    /**
     * @description:4
     * @return {*}
     * @Author: dingtao
     * @Date: 2023-11-24 11:39:25
     */
    handleClose() {
      chrome.storage.local.get('refuseComment', (data) => {
        if (data.refuseComment) {
          chrome.storage.local.set({
            refuseComment: {
              refuseCommentCount: data.refuseComment.refuseCommentCount + 1,
              refuseCommentTime: new Date().getTime(),
              isComments: false
            }
          })
        } else {
          chrome.storage.local.set({
            refuseComment: {
              refuseCommentCount: 1,
              refuseCommentTime: new Date().getTime(),
              isComments: false
            }
          })
        }
      })
      this.commentsDialogVisible = false
    },

    /**
     * @description: 4  
     * @return {*}
     * @Author: dingtao
     * @Date: 2023-11-24 11:38:03
     */
    submitComments() {
      this.handleClose()
      this.$sendLog(this.logId, {
        commentsValue: this.commentsValue,
        commentsText: this.commentsText
      })
    },
    /**
     * @description: 
     * @return {*}
     * @Author: dingtao
     * @Date: 2023-11-24 11:37:29
     */
    openChromeUrl() {
      this.$sendLog(this.logId, {
        commentsValue: this.commentsValue,
        commentsText: this.commentsText
      })
      this.commentsDialogVisible = false
      chrome.storage.local.set({
        refuseComment: {
          refuseCommentCount: 0,
          refuseCommentTime: new Date().getTime(),
          isComments: true
        }
      })
      window.open(this.googleUrl)
    },
    /**
     * @description:
     * @return {*}
     * @Author: dingtao
     * @Date: 2023-11-24 11:35:15
     */
    commentsInit() {
      // console.log('')
      const _This = this
      chrome.storage.local.get(['installTime', 'refuseComment', 'zbaseConfig'], (data) => {
        const commentsConfig = _This.getConfigData(data.zbaseConfig)
        // console.log('')
        if (data.refuseComment && commentsConfig && commentsConfig.commentsShow) {
          // console.log('')
          console.log(data.refuseComment)
          if (data.refuseComment.isComments) {
            // console.log('')
            _This.commentsDialogVisible = false
          } else if (
            data.refuseComment.refuseCommentCount < commentsConfig.refuseCommentCount &&
            _This.checkTime(data.refuseComment.refuseCommentTime, commentsConfig.rejectionInterval)
          ) {
            // console.log('')
            _This.commentsDialogVisible = true
          } else {
            // console.log('')
          }
        } else {
          // console.log('')
          if (commentsConfig && commentsConfig && commentsConfig.commentsShow) {
            if (_This.checkTime(data.installTime * 1000, commentsConfig.installationInterval)) {
              // console.log('')
              _This.commentsDialogVisible = true
            } else {
              // console.log('')
            }
          }
        }
      })
    },
    /**
     * @description: 
     * @param {*} time
     * @param {*} second
     * @return {*}
     * @Author: dingtao
     * @Date: 2023-11-24 11:35:52
     */
    checkTime(time, second) {
      console.log(time, second)
      const nowTime = new Date().getTime()
      if (time < nowTime - second * 1000) {
        return true
      } else return false
    },

    /**
     * @description:
     * @param {*} zbaseConfig 
     * @return {*}
     * @Author: dingtao
     * @Date: 2023-11-24 11:36:34
     */
    getConfigData(zbaseConfig) {
      const commentsConfigList = zbaseConfig.data.config.filter((item) => {
        return item.name === 'commentsConfig'
      })
      let commentsConfig = commentsConfigList.length !== 0 ? commentsConfigList[0] : null
      return commentsConfig.params ? commentsConfig.params : {}
    }
  }
}
</script>
<style scoped>
.m-comments-block >>> .el-rate__icon {
  font-size: 28px;
}

.m-comments-body {
  text-align: center;
  min-height: 130px;
}
.m-comments-body p {
  margin-bottom: 10px;
}

.m-comments-body div {
  margin: 10px auto;
}
.m-comments-body .m-comments-text {
  width: 300px;
}

.m-comments-body >>> .el-button--success {
  background-color: #37b64a;
  border-color: #37b64a;
  margin-top: 20px;
}
.m-comments-body >>> .el-button {
  padding: 8px 46px;
}
</style>
