<template>
  <div>
    <div class="whatsAppNumbers send-message-background">
      <el-card
        class="box-card whatsapp-numbers"
        shadow="always"
        :body-style="{ padding: '10px 10px 0 10px' }"
      >
        <div class="text item">
          <el-row style="height: 30px">
            <el-col :span="6">
              <p
                class="send-message-card-title whatsAppTitle titleText"
                v-text="whatsAppNumbersText"
              ></p>
            </el-col>
            <el-col :span="7" :offset="11"> </el-col>
          </el-row>
          <el-input
            class="textareaBack"
            placeholder="+8613135587131,+8619197646821"
            size="medium"
            v-model="whatsAppNumbersSimple"
            clearable
            slot="reference"
            @focus="handleWhatsAppNumbersFocus"
            @change="handleWhatsAppNumbersChange"
          >
          </el-input>
          <div class="remindTitle">
            <p class="remindText" v-if="remindTitle === 'normal'">
              {{ $t('country_code_t') }}
            </p>
            <p class="warnText" v-if="remindTitle === 'contentCheck'">
              <i class="exclamation">!</i> Please enter at least
              <i style="fontweight: bolder; fontstyle: normal">one</i> number.
            </p>
            <p class="warnText" v-if="remindTitle === 'formatCheck'">
              <i class="exclamation">!</i> Checking the number's format you entered.
              <el-tooltip placement="top">
                <div slot="content">
                  <i
                    class="el-icon-close"
                    style="color: rgba(255, 8, 8, 100); margin-right: 3px"
                  ></i>
                  <span style="color: #dcdfe6">{{ incorrectPhoneNum }}</span>
                  <br />
                  <i
                    class="el-icon-check"
                    style="color: rgba(79, 251, 106, 100); margin-right: 3px"
                  ></i>
                  <span style="color: #ffffff">+8613119140503</span>
                </div>
                <!-- <el-button>Top center</el-button> -->
                <i class="el-icon-question" style="color: rgba(255, 5, 5, 100)"></i>
              </el-tooltip>
            </p>
          </div>
        </div>
      </el-card>
    </div>
    <div class="messageText">
      <el-card
        class="box-card message-text"
        shadow="always"
        :body-style="{ padding: '10px 10px 14px 10px' }"
      >
        <p class="send-message-card-title titleText" v-text="messageTextText"></p>
        <el-select
          class="send-message-text-select textSelect"
          :placeholder="this.$t('select_template_t')"
          size="small"
          :no-data-text="'No Data'"
          v-model="messageTextTemplateSimple"
          @change="handleMessageTextTemplateChange"
          clearable
        >
          <el-option
            v-for="(item, index) in messageTextTemplateOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
            <template>
              <span @click="selectTemplate(item.label)">{{ item.label }}</span>
              <i @click.stop.self="handleEdit(index)" class="el-icon-delete"></i>
            </template>
          </el-option>
        </el-select>
        <el-button
          @click="saveAsTemplateSimpleClick"
          class="send-message-save-template-btn"
          type="success"
          size="small"
        >
          {{ saveAsTemplateText }}
        </el-button>
        <el-input
          id="text"
          class="textareaBack textareaForbidResize"
          type="textarea"
          :autosize="{ minRows: 6, maxRows: 6 }"
          v-model="messageTextSimple"
          @change="handleMessageTextChange"
        >
        </el-input>
      </el-card>
    </div>

    <div class="sendingSettings">
      <el-card
        class="box-card sending-setting"
        shadow="always"
        :body-style="{ padding: '10px 10px 14px 10px' }"
      >
        <el-row>
          <el-col :span="6">
            <span class="send-message-card-title titleText" v-text="sendingSettingsText"></span>
          </el-col>
          <el-col :span="16" style="margin: 1px auto">
            <span class="send-message-common-font" v-text="sendingSettingsNoticeText"></span>
          </el-col>
        </el-row>
        <el-row style="margin-top: 23px">
          <el-col :span="6" style="margin-top: 7px">
            <span
              class="send-message-card-title"
              v-text="sendingIntervalText"
              style="color: #c5c5c5"
            ></span>
          </el-col>
          <el-col :span="13">
            <el-input-number
              class="numSelect"
              v-model="minNumSimple"
              :precision="0"
              size="small"
              @change="handleMinNumChange"
              :min="6"
              :max="300"
              label=""
            ></el-input-number>
            ~
            <el-input-number
              class="numSelect"
              v-model="maxNumSimple"
              :precision="0"
              size="small"
              @change="handleMaxNumChange"
              :min="10"
              :max="300"
              label=""
            ></el-input-number>
            <span
              class="send-message-common-font"
              v-text="secondsText"
              style="font-size: 14px; margin-left: 4px"
            ></span>
          </el-col>
          <el-col :span="5" class="common-success-btn" :style="{ textAlign: 'center' }">
            <!-- <el-button
              type="success"
              @click="handleClickShowStatistics"
              plain
              size="small"
              style="height: 32px; width: 110px"
              >{{ $t('statistics_t') }}
            </el-button> -->
          </el-col>
        </el-row>
        <el-row style="margin-top: 23px">
          <el-col :span="18" class="common-success-btn" :style="{ textAlign: 'center' }">
            <el-button
              v-if="stopFlagSimple === true"
              style="width: 100%"
              type="success"
              @click="sendClick"
              plain
              size="small"
              >{{ $t('send_t') }}
            </el-button>
            <el-button
              v-else-if="stopFlagSimple === false"
              style="cursor: no-drop; width: 100%"
              plain
              disabled
              type="success"
              size="small"
              >{{ $t('sending_t') }}
            </el-button>
            <el-button
              v-else-if="stopFlagSimple === 'disabled'"
              disabled
              type="success"
              style="cursor: no-drop; width: 100%"
              size="small"
              class="disableBtn"
              >{{ $t('send_t') }}
            </el-button>
          </el-col>

          <el-col :span="6" style="text-align: center">
            <el-button
              type="text"
              @click="handleClickShowStatistics"
              style="color:#37b64a;border-bottom:1px solid #37b64a;border-radius: 0;padding: 0;margin-top: 12px;"
              >
              {{ $t('statistics_t') }}
              <i class="el-icon-right"></i>
            </el-button>
          </el-col>

        </el-row>
      </el-card>
    </div>
  </div>
</template>

<script>
import {
  phoneNumFormatCheck,
  phoneNumContentCheck,
  phoneNumNormal
} from '@/utils/phone-num-check-utils'
import { EXTENSION_NAME, LOGSTORE, WAPLUS_REMOVE_FLAG } from '@/utils/extension-util'
import { LIMIT_POPUP_CRM } from '@/utils/common/config'
import { loggerFeatureId } from '@/utils/log-util'
import { getBrowser } from '@/utils/browser-util'
export default {
  name: 'SendMessages',
  props: ['permissionText', 'setShowStatistics'],
  data() {
    return {
      // whatsAppNumbersText: this.$t('whatsapp_numbers_t'),
      whatsAppNumbersSimple: '',
      // messageTextText: this.$t('message_text_t'),
      messageTextTemplateSimple: '',
      messageTextTemplateOptions: [],
      // saveAsTemplateText: this.$t('save_as_t'),
      messageTextSimple: '',
      // sendingSettingsText: this.$t('sending_settings_t'),
      // sendingSettingsNoticeText: this.$t('messages_will_t'),
      // sendingIntervalText: this.$t('sending_interval_t'),
      minNumSimple: 6,
      maxNumSimple: 10,
      // secondsText: this.$t('seconds_t'),
      stopFlagSimple: true,
      sendMessageSimple: 'simple',
      remindTitle: 'normal',
      incorrectPhoneNum: '55555',
      safetyChecked: false,
      simpleChangeNum: 0,
      incorrectPhoneNumArr: [],
      contentArr: [],
      extensionName: EXTENSION_NAME,
      waplus_remove_flag: WAPLUS_REMOVE_FLAG
    }
  },
  computed: {
    whatsAppNumbersText() {
      return this.$t('whatsapp_numbers_t')
    },
    messageTextText() {
      return this.$t('message_text_t') 
    },
    saveAsTemplateText() {
      return this.$t('save_as_t')
    },
    sendingSettingsText() {
      return this.$t('sending_settings_t')
    },
    sendingSettingsNoticeText() {
      return this.$t('messages_will_t')
    },
    sendingIntervalText() {
      return this.$t('sending_interval_t')
    },
    secondsText() {
      return this.$t('seconds_t')
    }
  },
  watch: {
    '$i18n.locale': {
      async handler(newVal) {
        let messageText = await chrome.storage.local.get(['messageTextSimple'])
        if (messageText.messageTextSimple === undefined) {
          this.messageTextSimple = this.$t('help_there_t')
        }
      }
    }
  },
  created: async function () {
    const _this = this
    chrome.storage.local.get(
      [
        'whatsAppNumbersSimple',
        'minNumSimple',
        'maxNumSimple',
        'messageTextTemplateSimple',
        'remindTitle',
        'incorrectPhoneNum',
        'simpleChangeNum',
        'messageTextTemplateOptions',
        'messageTextSimple',
        'stopFlagSimple'
      ],
      function (res) {
        _this.whatsAppNumbersSimple = res.whatsAppNumbersSimple
        _this.minNumSimple = res.minNumSimple || 6
        _this.maxNumSimple = res.maxNumSimple || 10
        _this.messageTextTemplateSimple = res.messageTextTemplateSimple
        _this.remindTitle = res.remindTitle || 'normal'
        _this.incorrectPhoneNum = res.incorrectPhoneNum || ''
        _this.simpleChangeNum = res.simpleChangeNum || 0
        // todo remindTitle
        if (_this.remindTitle !== 'normal') {
          let remindBorder = document.querySelector(
            '.textareaBack.el-input.el-input--medium.el-input--suffix>.el-input__inner'
          )
          remindBorder.style.borderColor = 'rgba(255, 5, 5, 100)'
        }
        // 
        _this.messageTextTemplateOptions = res.messageTextTemplateOptions || [
          {
            value: _this.$t('help_there_t'),
            label: _this.$t('help_there_t')
          }
        ]
        if (res.messageTextSimple === undefined) {
          _this.messageTextSimple = _this.$t('help_there_t')
        } else {
          _this.messageTextSimple = res.messageTextSimple
        }

        if (res.stopFlagSimple === undefined) {
          _this.stopFlagSimple = true
        } else {
          _this.stopFlagSimple = res.stopFlagSimple
        }
        // content{stopFlagSimple: true}
        const sendData = {
          stopFlagSimple: _this.stopFlagSimple
        }
        _this.$parent.sendDataPopToContent(sendData)
      }
    )
  },
  methods: {
    /**
     * @description: 
     * @return {*}
     */
    handleClickShowStatistics() {
      this.setShowStatistics && this.setShowStatistics(true)
    },
    // LeadsLink
    goLinkLeads(link) {
      window.open(link)
    },
    openPricing() {
      this.$sendLog(908302, { s_feature_id: loggerFeatureId.SEND_MESSAGE })
      this.$emit('openPricing')
    },
    // WhatsApp
    handleWhatsAppNumbersFocus() {
      let remindBorder = document.querySelector(
        '.textareaBack.el-input.el-input--medium.el-input--suffix>.el-input__inner'
      )
      this.remindTitle = 'normal'
      remindBorder.style.borderColor = 'rgb(220, 223, 230)'
    },
    // 
    handleWhatsAppNumbersChange(value) {
      if (!value) {
        // ,
        phoneNumContentCheck(value, this)
        // 
        this.$sendLog(301302, {
          s_content: [],
          s_incorrect_phone_num: [],
          s_status: 1
        })
      } else {
        // inputinput
        phoneNumNormal(this)
        phoneNumFormatCheck(value, this)
        // 
        if (this.remindTitle !== 'normal') {
          this.$sendLog(901302, {
            s_content: this.contentArr,
            s_incorrect_phone_num: this.incorrectPhoneNumArr,
            s_status: 2
          })
        } else {
          this.$sendLog(901303)
        }
      }
      // 
      this.simpleChangeNum++
      chrome.storage.local.set({
        whatsAppNumbersSimple: value,
        simpleChangeNum: this.simpleChangeNum - 1
      })
    },
    // 
    handleEdit(index) {
      this.messageTextTemplateOptions.splice(index, 1)
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions
      })
    },
    // 
    handleMessageTextTemplateChange(value) {
      this.messageTextSimple = value
      chrome.storage.local.set({
        messageTextSimple: this.messageTextSimple,
        //todo messageTextTemplateSimple
        messageTextTemplateSimple: this.messageTextTemplateSimple
      })
    },
    //todo 
    selectTemplate(value) {
      this.messageTextSimple = value
      chrome.storage.local.set({
        messageTextSimple: this.messageTextSimple,
        messageTextTemplateSimple: this.messageTextTemplateSimple
      })
    },
    // 
    saveAsTemplateSimpleClick() {
      this.$sendLog(901201)
      if (this.messageTextSimple.replace(/\s/g, '').length) {
        let tmp = {
          value: this.messageTextSimple,
          label: this.messageTextSimple
        }
        let flag = 0
        for (let templates of this.messageTextTemplateOptions) {
          if (templates.value === tmp.value) flag = 1
        }
        // todo 
        if (flag) {
          this.$confirm('', 'The template has been saved.', {
            customClass: 'emptyContentTemplate',
            showConfirmButton: false,
            showCancelButton: false,
            center: true,
            type: 'warning'
          })
        } else {
          document
            .getElementsByClassName('send-message-save-template-btn')[0]
            .style.setProperty('animation', 'none')
          document
            .getElementsByClassName('send-message-save-template-btn')[0]
            .style.setProperty('-webkit-animation', 'none')
          setTimeout(function () {
            document
              .getElementsByClassName('send-message-save-template-btn')[0]
              .style.setProperty('animation', 'mymove 1s')
            document
              .getElementsByClassName('send-message-save-template-btn')[0]
              .style.setProperty('-webkit-animation', 'mymove 1s')
            const keyframes1 = '@keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const keyframes2 =
              '@-webkit-keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const style = document.createElement('style')
            style.type = 'text/css'
            style.innerHTML = keyframes1
            document.getElementsByClassName('send-message-save-template-btn')[0].appendChild(style)
            style.innerHTML = keyframes2
            document.getElementsByClassName('send-message-save-template-btn')[0].appendChild(style)
          }, 1)
          this.messageTextTemplateOptions.push(tmp)
        }
      }
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions
      })
    },
    // storage
    handleMessageTextChange(value) {
      chrome.storage.local.set({ messageTextSimple: value })
    },
    // 
    handleMinNumChange(value) {
      this.$nextTick(() => {
        if (value >= this.maxNumSimple) {
          this.minNumSimple = this.maxNumSimple
        }
        chrome.storage.local.set({ minNumSimple: this.minNumSimple })
      })
    },
    // 
    handleMaxNumChange(value) {
      this.$nextTick(() => {
        if (value <= this.minNumSimple) {
          this.maxNumSimple = this.minNumSimple
        }
        chrome.storage.local.set({ maxNumSimple: this.maxNumSimple })
      })
    },
    async addUseExportCount() {
      const browserInfo = getBrowser()
      if (this.permissionText === 'Free' && browserInfo?.browser === 'Chrome') {
        let { useExportCount = 0 } = await chrome.storage.local.get(['useExportCount'])
        const count = (useExportCount += 1)

        chrome.storage.local.set({
          useExportCount: count
        })
      }
    },
    async isFreeUserExportPermissions() {
      let data = true
      const browserInfo = getBrowser()
      if (browserInfo?.browser === 'Chrome') {
        const { useExportCount = 0, isShowFreeGift = false } = await chrome.storage.local.get([
          'useExportCount',
          'isShowFreeGift'
        ])

        if (useExportCount >= 1 && isShowFreeGift) {
          const tab = await chrome.tabs.query({ active: true, currentWindow: true })
          // tab
          const { isAWeekAgoInstall, havePermissionInfo, isCommentFinished } =
            await this.$bridge?.sendMessage(
              'POP_TO_CONTENT_GET_INSTALL_AND_PERMISSION_INFO',
              {},
              `content-script@${tab[0].id}`
            )
          if (isAWeekAgoInstall && !havePermissionInfo && !isCommentFinished) {
            this.$bridge?.sendMessage(
              'POP_TO_CONTENT_SHOW_COMMENT_DIALOG',
              {},
              `content-script@${tab[0].id}`
            )
            data = false
          }
        }
      }
      return data
    },
    async sendClick() {
      // 
      if (!this.$parent.firstSendTime) {
        chrome.storage.local
          .set({
            firstSendTime: Math.round(new Date() / 1000)
          })
          .then((r) => {})
      }
      this.$sendLog(901202)
      // todo 
      this.$parent.updateIsShowContinueBtn(true)
      // 
      let content
      if (this.messageTextSimple) {
        content = this.messageTextSimple.trim()
      } else {
        content = this.messageTextSimple
      }
      if (!content) {
        this.$confirm('', 'Please enter a message or upload an attachment.', {
          customClass: 'emptyContent',
          showConfirmButton: false,
          showCancelButton: false,
          center: true,
          type: 'warning'
        })
      } else if (!this.whatsAppNumbersSimple) {
        // 
        phoneNumContentCheck(this.whatsAppNumbersSimple, this)
      } else {
        if (this.remindTitle === 'normal') {
          phoneNumNormal(this)
        } else {
          return false
        }
        const freeUserExportPermissions = await this.isFreeUserExportPermissions()
        if (!freeUserExportPermissions) return
        this.$parent.sendMessageType = this.sendMessageSimple
        // this.$sendLog(301303,{s_amended_count:this.simpleChangeNum})
        //send
        this.$sendLog(901304)
        this.simpleChangeNum = 0
        // 
        chrome.storage.local
          .remove(['simpleChangeNum', 'confirmSendData', 'confirmSetData'])
          .then((r) => {})
        // 
        await this.$parent.handleConfirmClick()
        this.addUseExportCount()
        const _this = this
        chrome.storage.local.get(
          ['isShowSafetyWarningFlagValue', 'dialogVisible', 'confirmSetData', 'confirmSendData'],
          (res) => {
            if (
              res.isShowSafetyWarningFlagValue ||
              res.isShowSafetyWarningFlagValue === undefined
            ) {
              _this.$parent.isShowSafetyWarningFlagValue = true
            } else {
              // _this.$parent.activeName = 'statistics'
              if (res.dialogVisible === false) {
                if (res.confirmSetData) {
                  chrome.storage.local.set(res.confirmSetData)
                }
                if (typeof res.confirmSendData === 'object' && res.confirmSendData !== null) {
                  _this.$parent.sendDataPopToContent(res.confirmSendData)
                  _this.handleClickShowStatistics()
                }
              } else {
                _this.$parent.dialogVisible = true
              }
            }
          }
        )
      }
    }
  }
}
</script>

