<template>
  <div
    :class="{
      'pro-send-message-container': true,
      'is-schedule-message-show': isScheduleMessageIng
    }"
  >
    <div>
      <el-card
        class="whatsapp-numbers"
        shadow="always"
        :body-style="{ padding: '10px 10px 0 10px' }"
      >
        <div>
          <div class="send-message-card-title whatsAppTitle">
            <span v-text="whatsAppNumbersText" class="titleText"></span>
            <span
              v-if="inputPhoneNumFlag === 'content'"
              :class="{
                warnNumText: inputPhoneNum === 0,
                normalNumText: inputPhoneNum
              }"
            >
              <span v-text="inputPhoneNum" class="numText"></span>
              <span style="margin-right: 4px">Numbers</span>
            </span>
          </div>
          <div class="remindText">
            <div>{{ $t('country_code_t') }}</div>
          </div>
          <el-input
            class="textareaBackPro"
            placeholder="+8613135587131,+8619197646821"
            size="medium"
            v-model="whatsAppNumbers"
            clearable
            slot="reference"
            @change="handleWhatsAppNumbersChange"
            @focus="handleWhatsAppNumbersFocus"
          >
          </el-input>
          <div class="remindTitle">
            <p class="warnText" v-if="remindTitlePro === 'contentCheck'">
              <i class="exclamation">!</i> Please enter at least
              <i style="fontweight: bolder; fontstyle: normal">one</i> number.
            </p>
            <p class="warnText" v-if="remindTitlePro === 'formatCheck'">
              <i class="exclamation">!</i> Checking the number's format you entered.
              <el-tooltip placement="top">
                <div slot="content">
                  <i
                    class="el-icon-close"
                    style="color: rgba(255, 8, 8, 100); margin-right: 3px"
                  ></i>
                  <span style="color: #dcdfe6">{{ incorrectPhoneNumPro }}</span>
                  <br />
                  <i
                    class="el-icon-check"
                    style="color: rgba(79, 251, 106, 100); margin-right: 3px"
                  ></i>
                  <span style="color: #ffffff">+8613119140503</span>
                </div>
                <i class="el-icon-question" style="color: rgba(255, 5, 5, 100)"></i>
              </el-tooltip>
            </p>
          </div>
          <div class="send-message-card-title whatsAppTitle excelTitle">
            <span v-text="whatsAppNumbersExcelText" class="titleText"></span>
            <span
              v-if="!uploadExcelValue"
              :class="{
                warnNumText: excelNumbersText === 0,
                normalNumText: excelNumbersText
              }"
            >
              <span v-text="excelNumbersText" class="numText"></span>
              <span style="margin-right: 4px">Numbers</span>
              <el-button
                type="text"
                icon="el-icon-delete"
                @click="deleteExcelClick"
                :style="{
                  padding: '0',
                  fontSize: '16px',
                  color: excelNumbersText == 0 ? 'rgba(255, 4, 5, 100)' : 'rgba(54, 183, 74, 100)'
                }"
              >
              </el-button>
            </span>
          </div>
          <div class="remindText">
            <div>
              <i class="serialNum"></i>
              {{ $t('download_excel_template_t') }}<i class="serialNum"></i>
              {{ $t('fill_it_t') }}<i class="serialNum"></i>
              {{ $t('upload_excel_t') }}&nbsp;&nbsp;
            </div>
          </div>
          <!-- excel -->
          <el-row :gutter="20">
            <el-col :span="12">
              <el-button
                class="excelBtn"
                size="small"
                type="success"
                @click="downloadTemplateClick"
                >{{ downloadTemplateText }}</el-button
              >
            </el-col>
            <el-col :span="12">
              <el-upload
                action=""
                accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                :auto-upload="false"
                :multiple="false"
                :on-change="uploadExcelClick"
                :limit="2"
                :show-file-list="false"
                style="text-align: left"
              >
                <el-tooltip :content="$t('exit_full_screen_tip')">
                  <el-button
                    size="small"
                    type="success"
                    @click="uploadExcelClickLog"
                    class="excelBtn"
                  >
                    {{ uploadExcelText }}
                  </el-button>
                </el-tooltip>
              </el-upload>
            </el-col>
          </el-row>

          <div class="remindTitle">
            <p
              class="warnText"
              style="margin-top: 3px; height: auto"
              v-if="!uploadExcelValue && excelNumbersText === 0"
            >
              <i class="exclamation">!</i>
              {{ $t('please_check_t') }}
              <el-tooltip placement="top">
                <div slot="content">
                  <span> download <i class="hoverFont">Excel Template</i>;</span>
                  <br />
                  <span> fill it;</span>
                  <br />
                  <span> <i class="hoverFont">Upload Excel</i>.</span>
                </div>
                <i class="el-icon-question" style="color: rgba(255, 5, 5, 100)"></i>
              </el-tooltip>
            </p>
          </div>

          <div class="sendToGroup whatsAppTitle">
            <span v-text="sendToGroupText" class="titleText"></span>
            <span
              v-if="groupConcats !== 'normal'"
              :class="{
                warnNumText: groupMembersNum === 0,
                normalNumText: groupMembersNum
              }"
            >
              <span v-text="groupMembersNum" class="numText"></span>
              <span style="margin-right: 4px">Numbers</span>
              <el-button
                type="text"
                icon="el-icon-delete"
                @click="deleteGroupClick"
                :style="{
                  padding: '0',
                  fontSize: '16px',
                  color: groupMembersNum === 0 ? 'rgba(255, 4, 5, 100)' : 'rgba(54, 183, 74, 100)'
                }"
              >
              </el-button>
            </span>
          </div>
          <el-row :style="{ margin: '4px auto 17px auto' }">
            <el-col :span="24">
              <el-select
                class="send-message-wan-line-select"
                :class="
                  groupMembersNum === 0 && groupConcats !== 'normal'
                    ? 'textareaBackProWarn'
                    : 'textareaBackPro'
                "
                v-model="groupInfo"
                :placeholder="this.$t('select_joined_send_t')"
                size="mini"
                :no-data-text="this.$t('no_data_t')"
                @change="handleGroupChange"
                @visible-change="handleGroupChangeLog"
              >
                <el-option
                  v-for="item in groupOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <div
            class="remindTitle sendGroupRemind"
            v-if="groupConcats !== 'normal' && groupMembersNum === 0"
          >
            <div class="warnText">
              <i class="exclamation">!</i> You were removed from the group chat.
            </div>
          </div>

          <!--     -->
          <div class="sendToGroup whatsAppTitle">
            <span v-text="sendToLabelText" class="titleText"></span>
            <span
              v-if="labelConcats !== 'normal'"
              :class="{
                warnNumText: labelInfo.count === 0,
                normalNumText: labelInfo.count
              }"
            >
              <span class="numText">{{ labelInfo.count }}</span>
              <span style="margin-right: 4px">Numbers</span>
              <el-button
                type="text"
                icon="el-icon-delete"
