                    : 'textareaBackPro'
                "
                v-model="labelInfo"
                :placeholder="this.$t('select_joined_send_label')"
                size="mini"
                :no-data-text="this.$t('no_data_t')"
                @change="handleLabelChange"
                @visible-change="handleLabelVisible"
