                <el-option
                  v-for="item in labelOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item"
                >
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <div class="remindTitle sendGroupRemind" v-if="showLabelWarn || labelInfo.count === 0">
            <div class="warnText">
              <i class="exclamation">!</i>
              {{
                isBusiness
                  ? warnTextLabel
