              <template>
                <span @click="selectTemplate(item.label)">{{ item.label }}</span>
                <i @click.stop.self="handleEdit(index)" class="el-icon-delete"></i>
              </template>
            </el-option>
          </el-select>
          <el-button @click="saveAsTemplateClick" class="saveTemplateBtn" type="success"
