                  background-color: #5dae50;
                  height: 18px;
                  line-height: 0;
                  font-size: 12px;
                  padding: 10px 20px;
                  margin: 5px;
                "
                @click="placeholderClick"
                >{{ excelRowValue }}</el-button
              >
            </div>
          </div>
          <div class="textareaContainer">
            <el-input
              id="text"
              class="textareaBackPro"
              type="textarea"
              :autosize="{ minRows: 5, maxRows: 10 }"
              v-model="messageText"
              @change="handleMessageTextChange"
            >
            </el-input>
            <!--  -->
            <el-container>
              <el-aside class="switchBack" style="padding: 0 5px 5px 0; text-align: right">
                <!--                <el-switch-->
                <!--                    class="switch"-->
                <!--                    @change="handleApologizeForBotherChange"-->
                <!--                    v-model="apologizeForBotherValue"-->
                <!--                    :active-text="this.$t('apologize_for_t')"-->
                <!--                >-->
                <!--                </el-switch>-->
                <!--                <el-switch-->
                <!--                    class="switch"-->
                <!--                    @change="handleTimeStampChange"-->
                <!--                    v-model="timeStampValue"-->
                <!--                    :active-text="this.$t('add_timestamp_t')"-->
                <!--                >-->
                <!--                </el-switch>-->
                <el-button
                  class="textareaBtn"
                  size="small"
                  type="success"
                  @click="handleApologizeForBotherClick"
                  >{{ $t('apologize_for_t') }}</el-button
                >
                <el-button
                  class="textareaBtn"
                  size="small"
                  type="success"
                  @click="handleTimeStampClick"
                  >{{ $t('add_timestamp_t') }}</el-button
                >
              </el-aside>
            </el-container>
          </div>

          <!--  -->
          <el-container style="height: 35px">
            <el-aside
              width="43.1%"
              style="
                font-style: normal;
                font-size: large;
                font-weight: normal;
                color: #37b64a;
                text-align: left;
                background-color: #ffffff;
                line-height: 35px;
                padding-left: 5px;
              "
              class="switchBack"
            >
              <el-switch
                class="switch"
                v-model="sendAttachmentsValue"
                :active-text="this.$t('send_attachments_t')"
                @change="handleAttachmentsChange"
              >
              </el-switch>
            </el-aside>

            <el-main
              style="text-align: left; padding: 0; background-color: #ffffff; line-height: 35px"
              class="radioBack"
            >
              <el-radio-group
                v-model="radio"
                v-if="sendAttachmentsValue"
                @change="handleImgDocChange"
                size="small"
                text-color="rgba(68, 187, 0, 100)"
              >
                <el-tooltip :content="$t('exit_full_screen_tip')">
                  <el-radio
                    @click.native="attachmentBtn('img')"
                    label="Image"
                    :disabled="
                      this.radio !== 'Image' && this.radio !== '' && this.radio !== 'Contact'
                    "
                    >{{ $t('image_t') }}
                  </el-radio>
                </el-tooltip>
                <el-tooltip :content="$t('exit_full_screen_tip')">
                  <el-radio
                    @click.native="attachmentBtn('video')"
                    label="Video"
                    :disabled="
                      this.radio !== 'Video' && this.radio !== '' && this.radio !== 'Contact'
                    "
                    >{{ $t('video_t') }}
                  </el-radio>
                </el-tooltip>
                <el-tooltip :content="$t('exit_full_screen_tip')">
                  <el-radio
                    @click.native="attachmentBtn('doc')"
                    label="Document"
                    :disabled="
                      this.radio !== 'Document' && this.radio !== '' && this.radio !== 'Contact'
                    "
                    >{{ $t('document_t') }}
                  </el-radio>
                </el-tooltip>
                <el-radio
                  @click.native="attachmentBtn('con')"
                  label="Contact"
                  :disabled="this.radio !== 'Contact' && this.radio !== ''"
                  >{{ $t('contact_t') }}
                </el-radio>
              </el-radio-group>
              <el-radio-group
                v-model="radio"
                v-else
                @change="handleImgDocChange"
                size="small"
                disabled
              >
                <el-radio label="Image">{{ $t('image_t') }}</el-radio>
                <el-radio label="Video">{{ $t('video_t') }}</el-radio>
                <el-radio label="Document">{{ $t('document_t') }}</el-radio>
                <el-radio label="Contact">{{ $t('contact_t') }}</el-radio>
              </el-radio-group>
            </el-main>
          </el-container>
          <div style="display: flex; width: 95%" v-if="attachmentDivHtml && radio">
            <div style="flex: 42.5"></div>
            <div style="flex: 51; background-color: #f1f1f1; display: flex">
              <div style="flex: 1; padding: 5px 0 3px 3px" v-html="attachmentDivHtml"></div>
              <el-button
                type="text"
                icon="el-icon-delete"
                @click="deleteAttachmentClick"
                :style="{
                  padding: '0 3px',
                  fontSize: '16px',
                  color: '#5DAE50',
                  backgroundColor: '#DCE8DA'
                }"
              >
              </el-button>
            </div>
          </div>

          <!--  -->
          <!-- <div
            style="width: 100%; margin-top: 4px"
            v-if="btnMsgConfig.isShow"
            @click="openTutorialUrl"
          >
            <el-tag
              type="danger"
              style="
                width: 100%;
                color: #d84b4b;
                font-size: 14px;
                display: flex;
                align-items: center;
              "
            >
              <i style="color: #37b64a" class="el-icon-message-solid"></i>
              <a
                style="
                  color: #37b64a;
                  padding-left: 5px;
                  text-decoration: underline;
                  white-space: nowrap;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  display: inline-block;
                  width: 100%;
                "
                target="_block"
                href="javascript:;"
                >{{ btnMsgConfig.description }}</a
              >
            </el-tag>
          </div> -->
          <!---hh-->
          <div class="send-step send-step-2 switchBack" style="display: flex; height: 35px">
            <div style="padding-left: 5px; display: flex; align-items: center">
              <el-badge value="beta" class="item">
                <el-switch
                  class="switch"
                  v-model="audioState"
                  :active-text="this.$t('Recording')"
                  @change="startAudio"
                >
                </el-switch>
              </el-badge>
            </div>
            <div
              style="
                flex: 1;
                font-weight: 400;
                display: flex;
                align-items: center;
                padding-left: 115px;
              "
              v-if="audioMsg.time"
            >
              <span
                @click="deleAudioClick"
                style="
                  font-weight: 400;
                  font-size: 16px;
                  cursor: pointer;
                  padding: 0 10px;
                  color: #5dae50;
                "
                class="el-icon-delete"
              ></span>
              <span style="font-weight: 400; margin-right: 5px">Recording</span>
              <span style="font-weight: 400">{{ audioMsg.time }} s</span>
            </div>
          </div>
          <!--  -->
          <!-- <el-container style="height: 35px">
            <el-aside
              width="43.1%"
              style="
                font-style: normal;
                font-size: large;
                font-weight: normal;
                color: #37b64a;
                text-align: left;
                background-color: #ffffff;
                line-height: 35px;
                padding-left: 5px;
              "
              class="switchBack"
            >
              <el-switch
                class="switch"
                v-model="sendButtonValue"
                :active-text="this.$t('button_attachments_t')"
                @change="buttonAttachmentsChange"
                :disabled="btnMsgConfig.dysfunction"
              >
              </el-switch>
            </el-aside>

            <el-main
              style="text-align: left; padding: 0; background-color: #ffffff; line-height: 35px"
              class="radioBack"
            >
              <el-radio-group
                v-model="button_radio"
                v-if="sendButtonValue"
                @change="buttonMesChange"
                size="small"
                text-color="rgba(68, 187, 0, 100)"
              >
                <el-radio label="Text" style="margin-right: 22px"
                  >{{ $t('button_text_t') }}
                </el-radio>
                <el-radio label="Link" style="margin-right: 22px"
                  >{{ $t('button_link_t') }}
                </el-radio>
                <el-radio label="Call" style="margin-right: 22px"
                  >{{ $t('button_call_t') }}
                </el-radio>
              </el-radio-group>
              <el-radio-group
                v-model="button_radio"
                v-else
                @change="buttonMesChange"
                size="small"
                disabled
              >
                <el-radio label="Text" style="margin-right: 22px">{{
                  $t('button_text_t')
                }}</el-radio>
                <el-radio label="Link" style="margin-right: 22px">{{
                  $t('button_link_t')
                }}</el-radio>
                <el-radio label="Call" style="margin-right: 22px">{{
                  $t('button_call_t')
                }}</el-radio>
              </el-radio-group>
            </el-main>
          </el-container> -->
          <div v-if="sendButtonValue">
            <el-input
              style="width: 40%; margin-right: 10px"
              class="textareaBackPro"
              :placeholder="buttonInputPlace"
              size="medium"
              v-model="buttonInputValue"
              @change="buttonInputChange"
              slot="reference"
            >
            </el-input>
            <span v-if="button_radio === 'Text'">{{ $t('button_text_tip') }}</span>
            <span v-if="button_radio === 'Link'">{{ $t('button_link_tip') }}</span>
            <span v-if="button_radio === 'Call'">{{ $t('button_call_tip') }}</span>
            <span @click="ExampleClick" style="color: #0f40f5; cursor: pointer">
              {{ $t('Example') }}</span
            >
            <span
              v-if="buttonInputValue !== ''"
              @click="deleButtonClick"
              style="font-size: 16px; cursor: pointer; padding-left: 10px"
              class="el-icon-delete"
            ></span>
          </div>
        </el-card>
      </div>

      <el-card class="sendingSettings" :body-style="{ padding: '10px', marginTop: '10px' }">
        <el-row>
          <el-col :span="6">
            <span class="send-message-card-title titleText" v-text="sendingSettingsText"></span>
          </el-col>
          <el-col :span="16" style="margin: 1px auto">
            <span class="send-message-common-font" v-text="sendingSettingsNoticeText"></span>
          </el-col>
        </el-row>
        <el-row style="margin-top: 23px">
          <el-col :span="6" style="margin-top: 7px">
            <span
              class="send-message-card-title"
              v-text="sendingIntervalText"
              style="color: #c5c5c5"
            ></span>
          </el-col>
          <el-col :span="13">
            <el-input-number
              v-model="minNum"
              size="small"
              :precision="0"
              @change="handleMinNumChange"
              :min="6"
              :max="300"
              label=""
              class="numSelect"
            ></el-input-number>
            ~
            <el-input-number
              v-model="maxNum"
              size="small"
              :precision="0"
              @change="handleMaxNumChange"
              :min="10"
              :max="300"
              label=""
              class="numSelect"
            ></el-input-number>
            <span
              class="send-message-common-font"
              v-text="secondsText"
              style="font-size: 14px; margin-left: 4px"
            ></span>
          </el-col>
          <el-col :span="5" class="common-success-btn" :style="{ textAlign: 'center' }">
            <!-- <el-button
              type="success"
              @click="handleClickShowStatistics"
              plain
              size="small"
              style="height: 32px; width: 110px"
              >{{ $t('statistics_t') }}
            </el-button> -->
          </el-col>
        </el-row>
        <el-row>
