                style="
                  font-style: normal;
                  font-size: large;
                  font-weight: normal;
                  color: #37b64a;
                  text-align: left;
                  background-color: #ffffff;
                  line-height: 35px;
                  padding-left: 5px;
                "
                class="switchBack"
              >
                <el-switch
                  class="switch"
                  v-model="isDeleteMessage"
                  :active-text="this.$t('delete_message_after_send')"
                  @change="handleDeleteMessageChange"
                >
                </el-switch>
              </el-aside>
            </el-tooltip>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="18">
            <el-tooltip
              class="item"
              effect="dark"
              :content="this.$t('open_schedule_message_tip')"
              placement="top-start"
            >
              <el-aside
                width="100%"
                style="
                  font-style: normal;
                  font-size: large;
                  font-weight: normal;
                  color: #37b64a;
                  text-align: left;
                  background-color: #ffffff;
                  line-height: 35px;
                  padding-left: 5px;
                "
                class="switchBack"
              >
                <el-badge value="beta" class="bulk-schedule-el-badge">
                  <el-switch
                    class="switch"
                    v-model="isScheduleMessage"
                    :active-text="this.$t('open_schedule_message')"
                    @change="handleScheduleMessageChange"
                  >
                  </el-switch>
                </el-badge>
              </el-aside>
            </el-tooltip>
            <div class="choose-schedule-time" v-if="isScheduleMessage">
              <span class="schedule-time-text">{{ $t('choose_schedule_time_label') }}</span>
              <el-date-picker
                @change="handleScheduleTimeChange"
                v-model="scheduleTime"
                type="datetime"
                format="yyyy/MM/dd HH:mm"
                value-format="timestamp"
                placeholder="please select"
                :picker-options="pickerOptions"
              >
              </el-date-picker>
            </div>
          </el-col>
        </el-row>
        <el-row style="margin-top: 23px">
          <el-col :span="18" class="common-success-btn">
            <div class="scheduleBtn" v-if="stopFlag === true">
              <el-button
                v-if="isScheduleMessage"
                type="primary"
                @click="scheduleBtnClick"
                plain
                style="width: 100%"
                >{{ $t('schedule_send_t') }}
              </el-button>
              <el-button
                v-else
                type="success"
                @click="
                  () => {
                    sendClick('now')
                  }
                "
                plain
                style="width: 100%"
                >{{ $t('send_t') }}
              </el-button>
            </div>
            <el-button
              v-else-if="stopFlag === false"
              style="cursor: no-drop; width: 100%"
              disabled
              type="success"
              plain
            >
              {{ $t('sending_t') }}
            </el-button>
            <el-button
              v-else-if="stopFlag === 'disabled'"
              disabled
              type="success"
              style="cursor: no-drop; width: 100%"
              class="disableBtn"
              >{{ $t('send_t') }}
            </el-button>
          </el-col>
          <el-col :span="6" style="text-align: center">
            <el-button
              type="text"
              @click="handleClickShowStatistics"
              style="color:#37b64a;border-bottom:1px solid #37b64a;border-radius: 0;padding: 0;margin-top: 12px;"
              >
              {{ $t('statistics_t') }}
              <i class="el-icon-right"></i>
            </el-button>
          </el-col>
        </el-row>
      </el-card>
    </div>
    <div class="schedule-loading" v-if="isScheduleMessageIng">
      <div class="schedule-loading-mask"></div>
      <div class="schedule-loading-content">
        <div class="schedule-loading-text">{{ scheduleCountdown?.formattedTime }}</div>
        <div class="schedule-loading-button" @click="cancelScheduleMessage">
          {{ $t('cancel_schedule_message') }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAllPhoneNumberList } from '@/utils/phone-number-util'
import { addGroupPhoneNumList } from '@/utils/group-phone-num-utils'
import { phoneNumNormalPro, phoneNumFormatCheckPro } from '@/utils/phone-num-check-utils'
import XLSX from 'xlsx'
import { browseDocumentFile, browseImageFiles, browseVideoFile } from '@/utils/base64'
import { WAPLUS_REMOVE_FLAG } from '@/utils/extension-util'
import { LIMIT_POPUP_CRM } from '@/utils/common/config'
import { loggerFeatureId } from '@/utils/log-util'
import { EXCEL_TEMPLATE_FILE_PATH_URL } from '@/service/constants'
import moment from 'moment'
let scheduleTimer = null

export default {
  name: 'SendMessagesPro',
  props: [
    'isBusiness',
    'groupOptions',
    'groupsMap',
    'labelOptions',
    'permissionText',
    'setShowStatistics'
  ],
  data() {
    return {
      scheduleTime: '', // 
      currentTime: '', // 
      isDeleteMessage: false, //
      isScheduleMessage: false, // 
      isScheduleMessageIng: false, // 
      warnTextLabel: 'You were removed from the label chat.',
      showLabelWarn: false,
      // sendToGroupText: this.$t('send_to_t'),
      // sendToLabelText: this.$t('send_to_label'),
      // whatsAppNumbersText: this.$t('whatsapp_numbers_t'),
      // whatsAppNumbersExcelText: this.$t('whatsapp_numbers_excel_t'),
      // Exceltrue
      uploadExcelValue: true,
      excelNumbersText: '',
      excelData: [],
      // uploadExcelText: this.$t('upload_excel_btn_t'),
      // downloadTemplateText: this.$t('excel_template_t'),
      whatsAppNumbers: '',
      //   isDisabledGroupSelector: false,
      groupInfo: '',
      labelInfo: {},
      // messageTextText: this.$t('message_text_t'),
      messageTextTemplate: '',
      messageTextTemplateOptions: [],
      sendMessagePro: 'pro',
      messageText: '',
      // saveAsTemplateText: this.$t('save_as_t'),
      sendAttachmentsValue: false,
      radio: '',
      attachmentText:
        'The attachment will be sent to your own account first, then open WA Plus Sender, click the "Send" button.',
      apologizeForBotherValue: false,
      placeholderTemplateOptions: [
        {
          value: 'value1',
          label: 'WhatsApp Number'
        },
        {
          value: 'value2',
          label: 'First Name'
        },
        {
          value: 'value3',
          label: 'Last Name'
        },
        {
          value: 'value4',
          label: 'Other'
        }
      ],
      // sendingSettingsText: this.$t('sending_settings_t'),
      // sendingSettingsNoticeText: this.$t('messages_will_t'),
      // sendingIntervalText: this.$t('sending_interval_t'),
      minNum: 6,
      maxNum: 10,
      // secondsText: this.$t('seconds_t'),
      stopFlag: true,
      // 
      remindTitlePro: 'normal',
      incorrectPhoneNumPro: '',
      inputPhoneNum: 0,
      inputPhoneNumFlag: 'normal',
      // 
      groupConcats: 'normal',
      labelConcats: 'normal',
      groupMembersNum: '',
      proChangeNum: 0,
      contentArrPro: [],
      incorrectPhoneNumArrPro: [],
      file: {},
      attachmentDivHtml: '',
      timeStampValue: false,
      excelRows: [],
      waplus_remove_flag: WAPLUS_REMOVE_FLAG,
      // 
      button_radio: '',
      sendButtonValue: false,
      buttonInputPlace: 'Yes | No | Not sure',
      buttonInputValue: '',
      btnMsgConfig: {
        description: 'Important Notice: Discontinuation of WhatsApp Button Feature',
        id: 1,
        url: 'https://wawebsender.com/how-to-using-buttons-in-whatsApp',
        // https://wawebsender.com/how-to-using-buttons-in-whatsApp
        // https://wawebsender.com/tutorial/crm-extension/announcement-button
        isShow: false,
        dysfunction: false // 
      },
      audioMsg: {
        time: ''
      },
      audioState: false,
      pickerOptions: {
        disabledDate(time) {
          return moment(time).isBefore(moment(), 'day') // 
        }
      }
    }
  },
  computed: {
    /**
     * @description: 
     * @return {*}
     */
    scheduleCountdown() {
      const startTime = moment(this.currentTime)
      const endTime = moment(this.scheduleTime).startOf('minute')
      const duration = moment.duration(endTime.diff(startTime))

      //  HH:mm:ss
      const hours = String(Math.floor(duration.asHours())).padStart(2, '0') // 
      const minutes = String(duration.minutes()).padStart(2, '0') // 
      const seconds = String(duration.seconds()).padStart(2, '0') // 
      const formattedTime = `${hours}:${minutes}:${seconds}`
      // 
      const totalSeconds = Math.floor(duration.asSeconds())
      return {
        formattedTime, // 
        totalSeconds // 
      }
    },
    sendToGroupText() {
      return this.$t('send_to_t')
    },
    sendToLabelText() {
      return this.$t('send_to_label')
    },
    whatsAppNumbersText() {
      return this.$t('whatsapp_numbers_t')
    },
    whatsAppNumbersExcelText() {
      return this.$t('whatsapp_numbers_excel_t')
    },
    uploadExcelText()  {
      return this.$t('upload_excel_btn_t')
    },
    downloadTemplateText() {
      return this.$t('excel_template_t')
    },
    messageTextText() {
      return this.$t('message_text_t')
    },
    saveAsTemplateText() {
      return this.$t('save_as_t')
    },
    sendingSettingsText() {
      return this.$t('sending_settings_t')
    },
    sendingSettingsNoticeText() {
      return this.$t('messages_will_t')
    },
    sendingIntervalText() {
      return this.$t('sending_interval_t')
    },
    secondsText() {
      return this.$t('seconds_t')
    }
  },
  watch: {
    '$i18n.locale': {
      async handler(newVal) {
        let messageText = await chrome.storage.local.get(['messageText'])
        if (messageText.messageText === undefined) {
          this.messageText = this.$t('help_there_t')
        }
      }
    }
  },
  methods: {
    cancelScheduleMessage() {
      this.$sendLog(902209)
      scheduleTimer && clearInterval(scheduleTimer)
      scheduleTimer = null
      this.isScheduleMessageIng = false
      chrome.storage.local.set({
        isScheduleMessageIng: this.isScheduleMessageIng,
        stopFlagSimple: true
      })
    },
    openScheduleSetInterval() {
      chrome.storage.local.set({ stopFlagSimple: 'disabled' })
      this.currentTime = moment().format('YYYY-MM-DD HH:mm:ss')
      scheduleTimer && clearInterval(scheduleTimer)
      scheduleTimer = setInterval(() => {
        this.currentTime = moment().format('YYYY-MM-DD HH:mm:ss')
        if (this.scheduleCountdown?.totalSeconds <= 0) {
          scheduleTimer && clearInterval(scheduleTimer)
          scheduleTimer = null
          this.isScheduleMessageIng = false
          this.setShowStatistics(true)
        }
      }, 1000)
    },
    /**
     * @description: 
     * @return {*}
     */
    scheduleBtnClick() {
      if (!this.scheduleTime) {
        this.$message({
          message: this.$t('choose_schedule_time_tip'),
          type: 'warning'
        })
        return
      }
      if (moment(this.scheduleTime).isBefore(moment())) {
        this.$message({
          message: this.$t('choose_schedule_time_warning_tip'),
          type: 'warning'
        })
      } else {
        this.sendClick('schedule')
        this.$sendLog(902208, {
          scheduleTime: this.scheduleTime
        })
      }
    },
    /**
     * @description: 
     * @return {*}
     */
    handleClickShowStatistics() {
      this.setShowStatistics && this.setShowStatistics(true)
    },
    deleAudioClick() {
      this.audioMsg.time = ''
      this.audioState = false
      chrome.storage.local.set({ audio: {} })
    },
    startAudio() {
      if (this.permissionText === 'Free') {
        this.audioState = false
        this.deleteAttachmentClick()
        this.$emit('openPayPermissionPopup', {
          featureId: loggerFeatureId.SEND_MESSAGE_PRO
        })
        return false
      }
      if (this.isSending) return
      clearTimeout(this.audiotimer)
      let that = this
      this.isSending = true
      chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
        that.isSending = false
        chrome.tabs.sendMessage(tabs[0].id, { audioControl: that.audioState ? 'start' : 'end' })
      })
      if (that.audioState) {
        that.audiotimer = setTimeout(() => {
          window.close()
        }, 200)
      } else {
        that.deleAudioClick()
      }
    },
    openTutorialUrl() {
      this.$sendLog(902207)
      window.open(this.btnMsgConfig.url)
    },
    openPricing() {
      this.$sendLog(908302, { s_feature_id: loggerFeatureId.SEND_MESSAGE_PRO })
      this.$emit('openPricing')
    },
    howToUse() {
      window.open('https://waplus.io/sender-help')
    },
    howToUsePersonalizedFiles() {
      window.open('https://waplus.io/sender-help#personalized')
    },
    // todo 
    selectedTemplateChange(value) {
      if (value) {
        let templateNode = document.querySelectorAll('.el-select-dropdown__item')
        let templateContent = document.querySelectorAll('.el-select-dropdown__item > span')
        templateNode.forEach((item) => {
          item.style.width = '372px'
        })
        templateContent.forEach((item) => {
          item.style.width = '352px'
        })
      }
    },
    // 
    uploadExcelClick(file, fileList) {
      if (fileList.length > 1) {
        fileList.splice(0, 1)
      }
      this.readExcel(file)
        .then((item) => {
          if (item && item.length > 0) {
            if (item[0] && item[0].sheet && item[0].sheet.length) {
              // excel
              let rows = Object.values(item[0].rows)
              let rowsTmp = []
              for (let row of rows) {
                row = row.replace(//g, '(').replace(//g, ')')
                if (
                  row
                    .match(/[^\s]+/g)
                    .join('')
                    .replace('(', '')
                    .replace(')', '')
                    .replace('', '')
                    .replace('', '')
                    .toLocaleLowerCase() === 'whatsappnumberwithcountrycode'
                ) {
                  row = 'WhatsApp Number'
                }
                // tips
                if (row !== 'Tips') {
                  rowsTmp.push(row)
                }
              }
              this.excelRows = rowsTmp
              let excelData = item[0].sheet
              excelData = this.dealExcelData(excelData)
              this.excelNumbersText = excelData.length
              //   chrome.storage.local.set({ excelData: excelData });
              this.uploadExcelValue = false
              chrome.storage.local.set({
                excelData: excelData,
                uploadExcelValue: false,
                excelRows: rowsTmp
              })
              if (excelData.length === 0) {
                this.$sendLog(902302, { s_status: 2 })
              } else {
                this.$sendLog(902302, { s_status: 1 })
              }
            } else {
              this.excelNumbersText = 0
              this.uploadExcelValue = false
              chrome.storage.local.set({
                excelData: [],
                uploadExcelValue: false,
                excelRows: []
              })
            }
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    // ExcelVue
    readExcel(file) {
      return new Promise(function (resolve, reject) {
        const reader = new FileReader()
        reader.onload = function (e) {
          const data = e.target.result
          this.wb = XLSX.read(data, {
            type: 'binary'
          })
          const result = []
          this.wb.SheetNames.forEach((sheetName) => {
            result.push({
              sheetName: sheetName,
              sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName]),
              rows: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName], {
                range: -1
              })[0]
            })
          })
          resolve(result)
        }
        reader.readAsBinaryString(file.raw)
      })
    },
    // ExcelExcel
    dealExcelData(excelData) {
      let result = []
      for (let i = 0; i < excelData.length; i++) {
        let data = excelData[i]
        // excel
        data = this.checkSpecialCharacters(data)
        if (data['WhatsApp Number']) {
          result.push(data)
        }
      }
      return result
    },
    // 
    uploadExcelClickLog() {
      this.$sendLog(902201)
    },
    // 
    deleteGroupClick() {
      chrome.storage.local.set({ groupConcats: 'normal' })
      this.groupConcats = 'normal'
      this.groupInfo = ''
      this.groupMembersNum = ''
      chrome.storage.local.remove(['groupMembersNum', 'groupInfo'])
    },

    // label
    deleteLabelClick() {
      chrome.storage.local.set({ labelConcats: 'normal' })
      this.labelConcats = 'normal'
      this.labelInfo = {}
      chrome.storage.local.remove(['labelInfo'])
    },

    // 
    deleteExcelClick() {
      chrome.storage.local.remove('excelData')
      this.uploadExcelValue = true
      chrome.storage.local.set({ uploadExcelValue: true })
    },
    // 
    async downloadTemplateClick() {
      this.$sendLog(902203)
      const a = document.createElement('a')
      a.setAttribute('download', 'WAPlusSenderTemplate.xlsx')
      const templateUrl = await chrome.runtime.getURL(EXCEL_TEMPLATE_FILE_PATH_URL)
      a.setAttribute('href', templateUrl)
      a.click()
    },
    // 
    handleWhatsAppNumbersFocus() {
      let remindBorder = document.querySelector(
        '.textareaBackPro.el-input.el-input--medium.el-input--suffix>.el-input__inner'
      )
      this.remindTitlePro = 'normal'
      this.inputPhoneNumFlag = 'noraml'
      remindBorder.style.borderColor = 'rgb(220, 223, 230)'
    },
    // 
    handleWhatsAppNumbersChange(value) {
      if (value) {
        value = value.trim()
        this.inputPhoneNumFlag = 'content'
        phoneNumNormalPro(this)
        phoneNumFormatCheckPro(value, this)
        if (this.remindTitlePro !== 'normal') {
          this.$sendLog(902308, {
            s_content: this.contentArrPro,
            s_incorrect_phone_num: this.incorrectPhoneNumArrPro,
            s_status: 2
          })
        } else {
          this.$sendLog(902309)
        }
      } else {
        this.inputPhoneNumFlag = 'normal'
        phoneNumNormalPro(this)
        this.$sendLog(902308, {
          s_content: [],
          s_incorrect_phone_num: [],
          s_status: 1
        })
        this.inputPhoneNum = 0
        chrome.storage.local.set({ inputPhoneNum: 0 })
      }
      this.proChangeNum++
      chrome.storage.local.set({
        whatsAppNumbers: this.whatsAppNumbers,
        inputPhoneNumFlag: this.inputPhoneNumFlag,
        proChangeNum: this.proChangeNum - 1
      })
    },
    handleLabelChange(e) {
      this.$parent.messageBoxClose()
      chrome.storage.local.set({
        labelInfo: e,
        labelConcats: 'content'
      })
      this.labelInfo = e
      this.labelConcats = 'content'
    },

    handleGroupChange() {
      this.groupConcats = 'content'
      this.$parent.messageBoxClose()
      let phoneNumberStr = ''
      let group = this.groupsMap[this.groupInfo]
      chrome.storage.local.set({ groupInfo: this.groupInfo })
      // let phoneNumberList = getAllPhoneNumberList(group)
      let phoneNumberList = group
      phoneNumberList.forEach((item, i) => {
        phoneNumberStr += '+' + item
        if (i < phoneNumberList.length - 1) {
          phoneNumberStr += ','
        }
      })
      if (phoneNumberStr.length === 0) {
        this.groupMembersNum = 0
        this.groupConcats = 'content'
        chrome.storage.local.set({
          groupMembersNum: 0,
          groupConcats: 'content',
          groupMembersPhoneList: phoneNumberList
        })
      } else {
        this.groupMembersNum = phoneNumberList.length
        chrome.storage.local.set({
          groupMembersNum: phoneNumberList.length,
          groupConcats: 'content',
          groupMembersPhoneList: phoneNumberList
        })
        addGroupPhoneNumList(phoneNumberStr)
      }
    },
    handleGroupChangeLog(value) {
      if (value) {
        this.$sendLog(902202)
        if (this.groupOptions.length === 0) {
          this.$sendLog(902303, { s_status: 2 })
        } else {
          this.$sendLog(902303, { s_status: 1 })
          let templateNode = document.querySelectorAll('.el-select-dropdown__item')
          let templateContent = document.querySelectorAll('.el-select-dropdown__item > span')
          templateNode.forEach((item) => {
            item.style.width = '100%'
          })
          templateContent.forEach((item) => {
            item.style.width = '100%'
          })
        }
      }
    },
    handleLabelVisible(value) {
      // 
      if (!this.isBusiness && value) {
        this.showLabelWarn = true
        //  
        let remindBorder = document.querySelector('.labelSelect .el-input--suffix .el-input__inner')
        remindBorder.style.borderColor = 'rgba(255, 5, 5, 100)'
      }
      // 
      if (value) {
        this.$sendLog(902206)
      }
      if (!this.labelOptions || this.labelOptions.length === 0) {
        this.$sendLog(903224)
      }
    },
    handleMessageTextTemplateChange(value) {
      this.messageText = value
      chrome.storage.local.set({
        messageText: this.messageText,
        messageTextTemplate: this.messageTextTemplate
      })
    },
    handleEdit(index) {
      this.messageTextTemplateOptions.splice(index, 1)
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions
      })
    },
    selectTemplate(value) {
      this.messageText = value
      chrome.storage.local.set({
        messageText: this.messageText,
        messageTextTemplate: this.messageTextTemplate
      })
    },
    saveAsTemplateClick() {
      this.$sendLog(902204)
      if (this.messageText.replace(/\s/g, '').length) {
        let tmp = {
          value: this.messageText,
          label: this.messageText
        }
        let flag = 0
        for (let templates of this.messageTextTemplateOptions) {
          if (templates.value === tmp.value) flag = 1
        }
        if (flag) {
          this.$confirm('', 'The template has been saved.', {
            customClass: 'emptyContentTemplate',
            showConfirmButton: false,
            showCancelButton: false,
            center: true,
            type: 'warning'
          })
        } else {
          document
            .getElementsByClassName('saveTemplateBtn')[0]
            .style.setProperty('animation', 'none')
          document
            .getElementsByClassName('saveTemplateBtn')[0]
            .style.setProperty('-webkit-animation', 'none')
          setTimeout(function () {
            document
              .getElementsByClassName('saveTemplateBtn')[0]
              .style.setProperty('animation', 'mymove 1s')
            document
              .getElementsByClassName('saveTemplateBtn')[0]
              .style.setProperty('-webkit-animation', 'mymove 1s')
            const keyframes1 = '@keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const keyframes2 =
              '@-webkit-keyframes mymove {50% {background: #37B64B;color: #FFFFFF}}'
            const style = document.createElement('style')
            style.type = 'text/css'
            style.innerHTML = keyframes1
            document.getElementsByClassName('saveTemplateBtn')[0].appendChild(style)
            style.innerHTML = keyframes2
            document.getElementsByClassName('saveTemplateBtn')[0].appendChild(style)
          }, 1)
          this.messageTextTemplateOptions.push(tmp)
        }
      }
      chrome.storage.local.set({
        messageTextTemplateOptions: this.messageTextTemplateOptions
      })
    },
    handleMessageTextChange(value) {
      chrome.storage.local.set({ messageText: value })
    },
    buttonAttachmentsChange(value) {
      // 
      if (value) {
        this.button_radio = 'Text'
        chrome.storage.local.set({
          sendButtonValue: value,
          button_radio: this.button_radio
        })
        this.$sendLog(902316)
      } else {
        this.button_radio = ''
        this.sendButtonValue = false
        chrome.storage.local.set({
          sendButtonValue: this.sendButtonValue,
          button_radio: this.button_radio,
          buttonInputValue: ''
        })
      }
    },
    buttonInputChange(value) {
      chrome.storage.local.set({
        sendButtonValue: this.sendButtonValue,
        button_radio: this.button_radio,
        buttonInputValue: value
      })
    },
    ExampleClick() {
      if (this.button_radio === 'Text') {
        this.buttonInputValue = 'Yes | No | Not sure'
      }
      if (this.button_radio === 'Link') {
        this.buttonInputValue = 'Visit us (https://wawebsender.com)'
      }
      if (this.button_radio === 'Call') {
        this.buttonInputValue = 'Call me (6143xxxxxxxx)'
      }
      chrome.storage.local.set({
        sendButtonValue: this.sendButtonValue,
        button_radio: this.button_radio,
        buttonInputValue: this.buttonInputValue
      })
    },
    deleButtonClick() {
      this.buttonInputValue = ''
      chrome.storage.local.set({
        sendButtonValue: this.sendButtonValue,
        button_radio: this.button_radio,
        buttonInputValue: ''
      })
    },
    handleDeleteMessageChange(value) {
      if (value) {
        // this.$sendLog(902304); todo
      } else {
        this.isDeleteMessage = false
      }
      chrome.storage.local.set({ isDeleteMessage: this.isDeleteMessage })
    },
    handleScheduleMessageChange(value) {
      if (!value) {
        this.isScheduleMessage = false
      }
      chrome.storage.local.set({ isScheduleMessage: this.isScheduleMessage })
    },
    handleScheduleTimeChange(val) {
      chrome.storage.local.set({
        scheduleTime: moment(val).startOf('minute').valueOf()
      })
    },
    handleAttachmentsChange(value) {
      if (value) {
        this.$sendLog(902304)
        chrome.storage.local.set({
          sendAttachmentsValue: this.sendAttachmentsValue
        })
      } else {
        this.radio = ''
        this.sendAttachmentsValue = false
        chrome.storage.local.set({
          sendAttachmentsValue: this.sendAttachmentsValue,
          radio: this.radio,
          attachmentDivHtml: '',
          allDataImg: [],
          allDataVideo: [],
          allDataDocument: []
        })
      }
    },
    buttonMesChange(value) {
      let buttonType = ''
      buttonType = value
      this.buttonInputValue = ''
      if (value === 'Text') {
        this.buttonInputPlace = 'Yes | No | Not sure'
      }
      if (value === 'Link') {
        this.buttonInputPlace = 'Visit us (https://wawebsender.com)'
      }
      if (value === 'Call') {
        this.buttonInputPlace = 'Call me (6143xxxxxxxx)'
      }
      chrome.storage.local.set({
        sendButtonValue: this.sendButtonValue,
        button_radio: this.button_radio,
        buttonInputValue: ''
      })
    },
    handleImgDocChange(value) {
      let mediaType = ''
      if (this.radio === 'Image') {
        this.$sendLog(902305, { s_status: 1 })
        mediaType = 'img'
        browseImageFiles(this)
      }
      if (this.radio === 'Video') {
        this.$sendLog(902305, { s_status: 2 })
        mediaType = 'video'
        browseVideoFile(this)
      }
      if (this.radio === 'Document') {
        this.$sendLog(902305, { s_status: 3 })
        mediaType = 'doc'
        browseDocumentFile(this)
      }
      if (this.radio === 'Contact') {
        this.$sendLog(902305, { s_status: 4 })
        mediaType = 'contact'
      }
      chrome.storage.local.set({
        sendAttachmentsValue: this.sendAttachmentsValue,
        radio: this.radio
      })
      const sendData = {
        sendAttachments: true,
        mediaType: mediaType
      }
      this.$parent.sendDataPopToContent(sendData)
    },
    attachmentBtn(flag) {
      if (flag === 'img') {
        this.radio === 'Image'
      } else if (flag === 'video') {
        this.radio === 'Video'
      } else if (flag === 'doc') {
        this.radio === 'Document'
      } else {
        this.radio === 'Contact'
      }
    },
    handleTimeStampClick() {
      this.$sendLog(302312)
      let tmpText = this.$t('sent_at_t')
      this.insert(tmpText)
      chrome.storage.local.set({
        messageText: this.messageText,
        apologizeForBotherValue: this.apologizeForBotherValue
      })
    },
    handleApologizeForBotherClick() {
      this.$sendLog(902306)
      let tmpText = this.$t('if_it_t')
      this.insert(tmpText)
      chrome.storage.local.set({
        messageText: this.messageText,
        apologizeForBotherValue: this.apologizeForBotherValue
      })
    },
    handleUsePlaceholderChange(value) {
      this.placeholderTemplateValue = 'value1'
      if (value) {
        this.$sendLog(902307)
        let insertText = '{{WhatsApp Number}}'
        this.insert(insertText)
      }
    },
    insert(value) {
      const textField = document.getElementById('text')
      if (textField.selectionStart || textField.selectionStart === 0) {
        let startPos = textField.selectionStart
        let endPos = textField.selectionEnd
        let content =
          textField.value.substring(0, startPos) +
          value +
          textField.value.substring(endPos, textField.value.length)
        this.messageText = content
        textField.focus()
        textField.setSelectionRange(endPos + value.length, endPos + value.length)
      } else {
        this.messageText += '' + value
      }
      chrome.storage.local.set({ messageText: this.messageText })
    },
    handlePlaceholderTemplateChange(value) {
      if (value === 'value1') {
        this.insert('{{WhatsApp Number}}')
      }
      if (value === 'value2') {
        this.insert('{{First Name}}')
      }
      if (value === 'value3') {
        this.insert('{{Last Name}}')
      }
      if (value === 'value4') {
        this.insert('{{Other}}')
      }
    },
    handleMinNumChange(value) {
      this.$nextTick(() => {
        if (value >= this.maxNum) {
          this.minNum = this.maxNum
        }
        chrome.storage.local.set({ minNum: this.minNum })
      })
    },
    handleMaxNumChange(value) {
      this.$nextTick(() => {
        if (value <= this.minNum) {
          this.maxNum = this.minNum
        }
        chrome.storage.local.set({ maxNum: this.maxNum })
      })
    },
    async sendClick(sendType) {
      if (this.permissionText === 'Free') {
        // 
        this.$emit('openPayPermissionPopup', {
          featureId:
            sendType === 'schedule'
              ? loggerFeatureId.SEND_SCHEDULE_MESSAGE
              : loggerFeatureId.SEND_MESSAGE_PRO
        })
        return
      }
      if (!this.$parent.firstSendTime) {
        chrome.storage.local
          .set({
            firstSendTime: Math.round(new Date() / 1000)
          })
          .then((r) => {})
      }
      this.$sendLog(902205)
      this.$parent.updateIsShowContinueBtn(true)
      let content
      if (this.messageText) {
        content = this.messageText.trim()
      } else {
        content = this.messageText
      }
      if (!content) {
        this.$confirm('', 'Please enter a message or upload an attachment.', {
          customClass: 'emptyContent',
          showConfirmButton: false,
          showCancelButton: false,
          center: true,
          type: 'warning'
        })
      } else if (
        !this.inputPhoneNum &&
        !this.excelNumbersText &&
        !this.groupMembersNum &&
        !this.labelInfo.count
      ) {
        this.$parent.concatsIsNull = true
      } else {
        let dailySendNums = this.$parent.dailySendNums
        this.$parent.sendMessageType = this.sendMessagePro
        // this.$sendLog(302309, {s_amended_count: this.proChangeNum})
        //send
        this.$sendLog(902311)
        this.proChangeNum = 0
        chrome.storage.local
          .remove(['proChangeNum', 'confirmSendData', 'confirmSetData'])
          .then((r) => {})
        await this.$parent.handleConfirmClick()

        const _this = this
        chrome.storage.local.get(
          [
            'isShowSafetyWarningFlagValue',
            'dialogVisible',
            'confirmSetData',
            'allDataImg',
            'allDataVideo',
            'allDataDocument',
            'confirmSendData',
            'audio'
          ],
          async (res) => {
            if (sendType === 'now') {
              if (
                res.isShowSafetyWarningFlagValue ||
                res.isShowSafetyWarningFlagValue === undefined
              ) {
                _this.$parent.isShowSafetyWarningFlagValue = true
              } else {
                if (res.dialogVisible === false) {
                  _this.handleSendLocalData()
                } else {
                  _this.$parent.dialogVisible = true
                }
              }
            } else if (sendType === 'schedule') {
              this.openScheduleSetInterval()
              this.isScheduleMessageIng = true
              chrome.storage.local.set({ isScheduleMessageIng: this.isScheduleMessageIng })
            }
          }
        )
      }
    },
    handleSendLocalData() {
      const _this = this
      chrome.storage.local.get(
        [
          'confirmSetData',
          'allDataImg',
          'allDataVideo',
          'allDataDocument',
          'confirmSendData',
          'audio'
        ],
        (res) => {
          if (res.confirmSetData) {
            chrome.storage.local.set(res.confirmSetData)
          }
          if (typeof res.confirmSendData === 'object' && res.confirmSendData !== null) {
            let confirmSendData = res.confirmSendData
            confirmSendData.allDataImg = res.allDataImg ? res.allDataImg : []
            confirmSendData.allDataVideo = res.allDataVideo ? res.allDataVideo : []
            confirmSendData.allDataDocument = res.allDataDocument ? res.allDataDocument : []
            confirmSendData.audio = res.audio || {}
            _this.$parent.sendDataPopToContent(res.confirmSendData)
            _this.handleClickShowStatistics()
          }
        }
      )
    },
    checkSpecialCharacters(dataTmp) {
      // WhatsApp Number(with country code)WhatsApp Number
      Object.keys(dataTmp).forEach((key, index) => {
        let keyA = key.replace(//g, '(').replace(//g, ')')
        if (
          keyA
            .match(/[^\s]+/g)
            .join('')
            .replace('(', '')
            .replace(')', '')
            .replace('', '')
            .replace('', '')
            .toLocaleLowerCase() === 'whatsappnumberwithcountrycode' &&
          dataTmp[key].toString().match(/.*[0-9]/gm)
        ) {
          dataTmp['WhatsApp Number'] = dataTmp[key]
          delete dataTmp[key]
        }
        // Tips
        if (key === 'Tips') {
          delete dataTmp['Tips']
        }
      })
      return dataTmp
    },
    deleteAttachmentClick() {
      this.radio = ''
      this.attachmentDivHtml = ''
      chrome.storage.local.set({
        radio: '',
        attachmentDivHtml: '',
        allDataImg: [],
        allDataVideo: [],
        allDataDocument: []
      })
    },
    placeholderClick(e) {
      let text = '{{' + e.target.innerText + '}}'
      this.insert(text)
    },
    getBtnMsgConfig(zbaseConfig) {
      let btnMsgResult = null
      if (zbaseConfig && zbaseConfig.data.config) {
        let btnMsgConfigs = zbaseConfig.data.config.filter((item) => {
          return item.name === 'buttonMessage'
        })
        btnMsgResult = btnMsgConfigs.length !== 0 ? btnMsgConfigs[0].params[0] : null
      }
      return btnMsgResult
    }
  },
  created: async function () {
    const _this = this
    chrome.storage.local.get(
      [
        'isDeleteMessage',
        'isScheduleMessage',
        'isScheduleMessageIng',
        'scheduleTime',
        'whatsAppNumbers',
        'messageText',
        'minNum',
        'maxNum',
        'sendButtonValue',
        'buttonInputValue',
        'button_radio',
        'sendAttachmentsValue',
        'radio',
        'remindTitlePro',
        'incorrectPhoneNumPro',
        'inputPhoneNum',
        'inputPhoneNumFlag',
        'groupMembersNum',
        'groupConcats',
        'proChangeNum',
        'attachmentDivHtml',
        'stopFlag',
        'uploadExcelValue',
        'excelData',
        'messageTextTemplateOptions',
        'messageTextTemplate',
        'apologizeForBotherValue',
        'groupOptions',
        'groupsMap',
        'groupInfo',
        'labelInfo',
        'labelConcats',
        'excelRows',
        'zbaseConfig',
        'audio'
      ],
      (res) => {
        _this.btnMsgConfig = _this.getBtnMsgConfig(res.zbaseConfig)

        // console.log('')
        // console.log(_this.btnMsgConfig)
        _this.isDeleteMessage = res.isDeleteMessage
        _this.isScheduleMessage = res.isScheduleMessage
        _this.scheduleTime = res.scheduleTime
        _this.isScheduleMessageIng = res.isScheduleMessageIng
        if (res.scheduleTime < new Date().getTime()) {
          _this.isScheduleMessageIng = false
          chrome.storage.local.set({ isScheduleMessageIng: _this.isScheduleMessageIng })
        }
        if (_this.isScheduleMessageIng) {
          _this.openScheduleSetInterval()
        }
        _this.whatsAppNumbers = res.whatsAppNumbers
        _this.messageText = res.messageText || ''
        _this.minNum = res.minNum ? res.minNum : 6
        _this.maxNum = res.maxNum ? res.maxNum : 10
        _this.sendAttachmentsValue = res.sendAttachmentsValue
        // TODO 
        if (_this.btnMsgConfig && !_this.btnMsgConfig.dysfunction) {
          _this.sendButtonValue = res.sendButtonValue
          _this.button_radio = res.button_radio ? res.button_radio : ''
          _this.buttonInputValue = res.buttonInputValue ? res.buttonInputValue : ''
        }

        _this.radio = res.radio ? res.radio : ''
        _this.remindTitlePro = res.remindTitlePro || 'normal'
        _this.incorrectPhoneNumPro = res.incorrectPhoneNumPro || ''
        _this.inputPhoneNum = res.inputPhoneNum || 0
        _this.inputPhoneNumFlag = res.inputPhoneNumFlag || 'normal'
        _this.groupMembersNum = res.groupMembersNum || 0
        _this.groupConcats = res.groupConcats || 'normal'
        _this.proChangeNum = res.proChangeNum || 0
        _this.attachmentDivHtml = res.attachmentDivHtml ? res.attachmentDivHtml : ''

        _this.audioMsg.time = res.audio?.time || ''
        if (_this.audioMsg.time) {
          this.audioState = true
        }

        if (_this.button_radio === 'Link') {
          _this.buttonInputPlace = 'Visit us (https://wawebsender.com)'
        } else if (_this.button_radio === 'Call') {
          _this.buttonInputPlace = 'Call me (6143xxxxxxxx)'
        }
        if (_this.remindTitlePro !== 'normal') {
          let remindBorder = document.querySelector(
            '.textareaBackPro.el-input.el-input--medium.el-input--suffix>.el-input__inner'
          )
          remindBorder.style.borderColor = 'rgba(255, 5, 5, 100)'
        }
        if (res.stopFlag === undefined) {
          _this.stopFlag = true
        } else {
          _this.stopFlag = res.stopFlag
        }

        if (res.uploadExcelValue === undefined) {
          _this.uploadExcelValue = true
        } else {
          _this.uploadExcelValue = res.uploadExcelValue
        }
        const sendData = {
          stopFlag: _this.stopFlag
        }
        _this.$parent.sendDataPopToContent(sendData)

        _this.excelNumbersText = res.excelData ? res.excelData.length : ''
        _this.messageTextTemplateOptions = res.messageTextTemplateOptions || [
          {
            value: _this.$t('help_there_t'),
            label: _this.$t('help_there_t')
          }
        ]
        if (res.messageText === undefined) {
          _this.messageText = _this.$t('help_there_t')
        } else {
          _this.messageText = res.messageText
        }
        _this.messageTextTemplate = res.messageTextTemplate
        _this.apologizeForBotherValue = res.apologizeForBotherValue
        _this.groupOptions = res.groupOptions !== undefined ? res.groupOptions : []
        _this.groupsMap = res.groupsMap || {}
        _this.groupInfo = res.groupInfo || ''

        _this.labelInfo = res.labelInfo || ''
        _this.labelConcats = res.labelConcats || 'normal'
        _this.excelRows = res.excelRows || []
      }
    )
  }
}
</script>

