<template>
  <el-dialog
    :visible.sync="isShowNoActive"
    width="425px"
    top="95px"
    custom-class="NoActiveDialog"
    :modal-append-to-body="false"
    :show-close="false"
    :close-on-click-modal="true"
    @close="closeNoActivePopup"
    center
    title="Friendly Activation Reminder"
  >
    <div style="word-break: break-word; font-size: 14px; color: #101010; line-height: 20px">
      <p>
        We have noticed that you subscribed to Bulk Sender, but have not yet activated your
        extension. You can activate it now using any of the activation codes below.
      </p>
      <div class="actionCodeDiv">
        <p v-for="(item, index) in actionCodeList" :key="index" class="actionCodeDivText">
          {{ item.code.slice(0, 4) + ' ' + item.code.slice(4) }}
          <span v-if="item.active_status === 1" style="background: gray; color: darkgray"
            >Actived</span
          >
          <span v-if="item.active_status === 0" @click="checkActiveCode(item.code)">Active</span>
        </p>
      </div>
      <p>
        Activating your extension
        <span style="color: rgba(240, 7, 232, 1)"
          >will unlock all features and ensure the best experience</span
        >. Don't miss out - activate now to start enjoying the full benefits of your subscription!
      </p>
      <p style="font-weight: bold; margin-top: 20px">
        Please let us know if you have any questions.
      </p>
      <p style="font-weight: bold; margin-top: 20px">Enjoy your Bulk Sender extension!</p>
      <p style="font-weight: bold">The Bulk Sender Team</p>
      <p></p>
    </div>
  </el-dialog>
</template>

<script>
import { permissionCheck } from '@/api/permission'

export default {
  name: 'NoActiveDialog',
  data() {
    return {
      actionCodeList: []
    }
  },
  props: ['isShowNoActive'],
  created() {
    let _that = this
    chrome.storage.local.get(['actionCodeList'], (res) => {
      _that.actionCodeList = res.actionCodeList || []
    })
  },
  methods: {
    closeNoActivePopup() {
      this.$emit('closeNoActivePopup')
    },
    checkActiveCode(activeCode) {
      this.$sendLog(908205)
      chrome.storage.local.get(['userPhoneNum'], (res) => {
        let whatsappNumber = res.userPhoneNum.replace(/\D/g, '')
        permissionCheck(whatsappNumber, activeCode).then((response) => {
          if (response.code === 100000) {
            chrome.storage.local.remove([
              'isShowNoSubscription',
              'isOneNoSubscription',
              'isShowNoActive',
              'actionCodeList'
            ])
            chrome.storage.local.set({ permissionInfo: response.data, paid_mark: true }, () => {})
            this.$emit(
              'changePermissionCode',
              response.data['plink_id'],
              response.data['transaction_id'],
              whatsappNumber
            )
            this.$emit('closeNoActivePopup')
            this.$message({
              message: 'Activation successful',
              type: 'success'
            })
          } else {
            this.$message({
              message: response.message,
              type: 'error'
            })
          }
        })
      })
    }
  }
}
</script>

