import { sendMessage } from 'webext-bridge/dist/popup'
export default {
  props: ['isShowDrainageDialog'],
  data: function () {
    return {
      isShowDialog: true
    }
  },
  methods: {
    async openUrl() {
      const tab = await chrome.tabs.query({
        active: true,
        currentWindow: true
      })
      const drainage = await sendMessage('P2C_SET_DRAINAGE', {}, `content-script@${tab[0].id}`)
      if (drainage) {
        window.open(
          'https://chromewebstore.google.com/detail/privacy-instellingen-what/okglcjoemdnmmnodbllbcfaebeedddod?utm_source=bulk_sender_draninage'
        )
      }
    }
  }
}
