<template>
  <div class="account-detection-box">
    <div class="account-detection-title">{{ $t('account_detection_title1') }}</div>
    <div class="account-detection-content">
      <el-row>
        <el-col :span="18">
          <el-input
            type="input"
            :placeholder="$t('account_detection_input_placeholder')"
            v-model="whatsAppNumberSimple"
          ></el-input>
        </el-col>
        <el-col :span="6" style="text-align: right">
          <el-button
            v-loading="loading"
            type="success"
            plain
            @click="handleWhatsAppNumbersSimple"
            >{{ $t('account_detection_btn1') }}</el-button
          >
        </el-col>
      </el-row>
      <div class="detection-status-text" v-if="showDetectionStatus">
        <span v-if="detectionStatus"
          ><i class="account-detection-icon el-icon-success"></i> {{ checkNum }}
          {{ $t('account_detection_normal_use') }}</span
        >
        <span v-else
          ><i class="account-detection-icon el-icon-error"></i> {{ checkNum }}
          {{ $t('account_detection_not_use') }}</span
        >
      </div>
    </div>
    <div class="account-detection-title">{{ $t('account_detection_title2') }}</div>
    <div class="account-detection-content">
      <el-row>
        <el-col :span="18">
          <el-upload
            class="upload-account-detection"
            action=""
            :on-change="handleFileChange"
            :on-remove="handleRemove"
            :before-upload="beforeUpload"
            accept=".xls,.xlsx"
            :multiple="false"
            :limit="1"
            :file-list="fileList"
          >
            <el-button size="small">{{ $t('account_detection_file_btn') }}</el-button>
          </el-upload>
        </el-col>
        <el-col :span="6" style="text-align: right">
          <el-button
            type="success"
            v-loading="batchDetectionIng"
            plain
            @click="handleWhatsAppNumbersExcelSimple"
            >{{ $t('account_detection_btn2') }}</el-button
          >
        </el-col>
      </el-row>
      <div class="excel-detection-status-text">
        <div v-if="batchDetectionIng">{{ $t('account_detection_ing') }}</div>
        <div v-if="batchDetectionIng" class="chinking">
          <el-row>
            <el-col :span="20">
              <div class="excel-detection-block">
                <el-slider
                  class="no-drag-slider"
                  :show-tooltip="false"
                  v-model="sliderVal"
                ></el-slider>
              </div>
            </el-col>
            <el-col :span="4" style="text-align: right; margin-top: 6px">
              <svg
                class="account-excel-detection-icon"
                xmlns="http://www.w3.org/2000/svg"
                xmlns:xlink="http://www.w3.org/1999/xlink"
                viewBox="0 0 32 32"
                width="14"
                height="14"
                style=""
                filter="none"
              >
                <g>
                  <path
                    d="M16 1.778c7.855 0 14.222 6.367 14.222 14.222s-6.367 14.222-14.222 14.222-14.222-6.367-14.222-14.222 6.367-14.222 14.222-14.222zM15.903 7.111c-0.318 0-0.584 0.241-0.616 0.557l-1.064 10.636-0.002 0.004 0.001 0-0 0.001 0.004-0 8.055 2.598c0.301 0.097 0.627-0.042 0.765-0.325l0.005-0.010c0.135-0.29 0.033-0.635-0.238-0.805l-5.426-3.407-0.869-8.691c-0.031-0.313-0.292-0.552-0.605-0.557z"
                    fill="rgba(55,182,74,1)"
                  />
                </g>
              </svg>
            </el-col>
          </el-row>
        </div>
        <div v-if="fileList?.length && sliderVal === 100 && !batchDetectionIng">
          <el-row>
            <el-col :span="20"
              >{{ $t('account_detection_done')
              }}<span class="export-account-excel" @click="handleExportExcel">{{
                $t('account_detection_export')
              }}</span>
            </el-col>
            <el-col :span="4" style="text-align: right">
              <i class="account-excel-detection-icon el-icon-success"></i>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as XLSX from 'xlsx'
import { sendMessage } from 'webext-bridge/dist/popup'
import { loggerFeatureId } from '@/utils/log-util'
export default {
  name: 'Accountdetection',
  props: ['handleCheckUserPermission'],
  data: function () {
    return {
      checkNum: '',
      whatsAppNumberSimple: '',
      showDetectionStatus: false,
      detectionStatus: false,
      loading: false,
      fileList: [],
      excelData: null,
      sliderVal: 0,
      batchDetectionIng: false
    }
  },
  methods: {
    /**
     * @description: 
     * @return {*}
     */
    getBatchDetectionStatus() {
      chrome.storage.local.get(['batchDetection'], (res) => {
        if (res.batchDetection) {
          const { batchDetectionIng, oldData, newData } = res.batchDetection
          this.sliderVal = (newData?.length / oldData?.length) * 100
          this.batchDetectionIng = batchDetectionIng

          if (batchDetectionIng) {
            setTimeout(() => {
              this.getBatchDetectionStatus()
            }, 500)
          }
        }
      })
    },

    initbatchDetectionOldData() {
      chrome.storage.local.set({
        batchDetection: {
          batchDetectionIng: false,
          oldData: [],
          newData: []
        }
      })
      this.sliderVal = 0
    },
    /**
     * @description: 
     * @return {*}
     */
    handleWhatsAppNumbersSimple: async function () {
      if (!this.handleCheckUserPermission(loggerFeatureId.ACCOUNT_DETECTION_ONE)) return
      if (!this.whatsAppNumberSimple) return
      this.loading = true

      const tab = await chrome.tabs.query({ active: true, currentWindow: true })

      const data = await sendMessage(
        'P2C_CHECK_NUMBER_STATUS',
        {
          numberList: [this.whatsAppNumberSimple]
        },
        `content-script@${tab[0].id}`
      )

      this.checkNum = data && data[0]?.number
      this.showDetectionStatus = true
      this.detectionStatus = data && data[0]?.status

      this.loading = false

      chrome.storage.local.set({
        oneDetection: {
          number: this.checkNum,
          status: this.detectionStatus
        }
      })

      this.$sendLog(903403, {
        checkType: 'one'
      })
    },
    /**
     * @description: 
     * @return {*}
     */
    async handleWhatsAppNumbersExcelSimple() {
      if (!this.handleCheckUserPermission(loggerFeatureId.ACCOUNT_DETECTION_BATCH)) return

      const tab = await chrome.tabs.query({ active: true, currentWindow: true })

      if (!this.excelData?.length) {
        return
      }

      this.batchDetectionIng = true

      chrome.storage.local.set({
        batchDetection: {
          batchDetectionIng: true,
          oldData: this.excelData,
          newData: []
        }
      })

      this.getBatchDetectionStatus()

      sendMessage(
        'P2C_CHECK_NUMBER_STATUS',
        {
          numberList: this.excelData,
          type: 'batch'
        },
        `content-script@${tab[0].id}`
      )

      this.$sendLog(903403, {
        checkType: 'batch'
      })
    },
    beforeUpload(file) {
      const isExcel =
        file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
        file.type === 'application/vnd.ms-excel'
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isExcel) {
        this.$message.error(this.$t('account_detection_type_error_tip'))
      }
      if (!isLt5M) {
        this.$message.error(this.$t('account_detection_size_error_tip'))
      }
      return isExcel && isLt5M
    },
    /**
     * @description: 
     * @param {*} file
     * @param {*} fileList
     * @return {*}
     */
    handleFileChange(file, fileList) {
      this.fileList = fileList

      chrome.storage.local.set({
        detectionFileList: fileList
      })
      const reader = new FileReader()
      reader.onload = (e) => {
        const data = new Uint8Array(e.target.result)
        const workbook = XLSX.read(data, { type: 'array' })
        const firstSheet = workbook.Sheets[workbook.SheetNames[0]]
        const jsonData = XLSX.utils.sheet_to_json(firstSheet, { header: 1 })
        const arrData = jsonData
          .map((v, i) => {
            return isNaN(Number(v[0])) ? '' : v[0]
          })
          .filter((v) => v)
        this.excelData = arrData || []
        this.initbatchDetectionOldData()
      }
      reader.readAsArrayBuffer(file.raw)
    },
    /**
     * @description: 
     * @param {*} _
     * @param {*} fileList
     * @return {*}
     */
    handleRemove(_, fileList) {
      this.fileList = fileList
      chrome.storage.local.set({
        detectionFileList: fileList
      })
      this.initbatchDetectionOldData()
      this.excelData = null
    },
    /**
     * @description: 
     * @return {*}
     */
    async handleExportExcel() {
      if (!this.handleCheckUserPermission(loggerFeatureId.ACCOUNT_DETECTION_EXPORT)) return

      const { batchDetection, detectionFileList } = await chrome.storage.local.get([
        'batchDetection',
        'detectionFileList'
      ])

      const fileName = (detectionFileList && detectionFileList[0]?.name) || ''

      const exportName = fileName ? 'exported_' + fileName : 'exported_data.xlsx'

      const { newData } = batchDetection
      if (!newData.length) {
        alert('No data to export!')
        return
      }

      const exportData = newData.map((v) => {
        return {
          'Whatsapp Number': '+' + v.number,
          'Number Status': v.status
            ? this.$t('account_detection_normal_use')
            : this.$t('account_detection_not_use')
        }
      })

      // JSON
      const worksheet = XLSX.utils.json_to_sheet(exportData, {
        header: ['Whatsapp Number', 'Number Status']
      })

      // 
      worksheet['!cols'] = [
        { wch: 20 }, // 'Whatsapp Number' 
        { wch: 18 } // 'Number Status' 
      ]

      // 
      const workbook = XLSX.utils.book_new()
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1')

      // Excel
      XLSX.writeFile(workbook, exportName)
      this.$sendLog(903405, {
        exportData
      })
    }
  },
  created() {
    chrome.storage.local.get(['oneDetection', 'detectionFileList'], (res) => {
      this.checkNum = res.oneDetection?.number || ''
      this.fileList = res.detectionFileList || []
    })

    this.getBatchDetectionStatus()
  }
}
</script>

