<template>
  <div class="auto-reply-box">
    <div class="auto-reply-item">
      {{ $t('bulk_auto_reply.open') }} <el-switch v-model="openAutoReply"></el-switch>
    </div>
    <div class="auto-reply-item">
      {{ $t('bulk_auto_reply.reply_content') }}
      <el-input type="textarea" v-model="replyContent" style="min-height: 120px"></el-input>
    </div>
    <div class="auto-reply-item">
      <el-checkbox v-model="chooseTimeRange"></el-checkbox> {{ $t('bulk_auto_reply.time_range') }}
      <el-time-picker
        v-model="effectiveStartTime"
        size="small"
        style="width: 160px"
        :placeholder="$t('bulk_auto_reply.start_time_tip')"
      >
      </el-time-picker>
      -
      <el-time-picker
        v-model="effectiveEndTime"
        size="small"
        style="width: 160px"
        :placeholder="$t('bulk_auto_reply.end_time_tip')"
      >
      </el-time-picker>
    </div>
    <div class="auto-reply-item">
      <el-checkbox v-model="onlyReplyOnce"></el-checkbox> {{ $t('bulk_auto_reply.only_once') }}
    </div>
    <div class="auto-reply-button">
      <el-button
        type="success"
        size="small"
        v-loading="saveBtnLoading"
        @click="handleSaveAutoReply"
        >{{ $t('bulk_auto_reply.btn') }}</el-button
      >
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import { loggerFeatureId } from '@/utils/log-util'
export default {
  name: 'AutoReply',
  props: ['handleCheckUserPermission'],
  data() {
    return {
      openAutoReply: false, // 
      replyContent: '', // 
      effectiveStartTime: null, // 
      effectiveEndTime: null, // 
      chooseTimeRange: false, // 
      onlyReplyOnce: false, // 
      saveBtnLoading: false
    }
  },
  methods: {
    handleSaveAutoReply() {
      if (!this.handleCheckUserPermission(loggerFeatureId.AUTO_REPLY_MESSAGE)) return

      if (this.openAutoReply && !this.replyContent) {
        this.$message.warring(this.$t('bulk_auto_reply.reply_content_tip'))
        return
      }
      if (this.chooseTimeRange && (!this.effectiveStartTime || !this.effectiveEndTime)) {
        this.$message.warring(this.$t('bulk_auto_reply.time_range_tip'))
        return
      }
      this.saveBtnLoading = true

      const effectiveTimeRangeData = [
        moment(this.effectiveStartTime).valueOf(),
        moment(this.effectiveEndTime).valueOf()
      ]
      chrome.storage.local.set(
        {
          bulk_openAutoReply: this.openAutoReply,
          bulk_replyContent: this.replyContent,
          bulk_chooseTimeRange: this.chooseTimeRange,
          bulk_onlyReplyOnce: this.onlyReplyOnce,
          bulk_effectiveTimeRange: effectiveTimeRangeData
        },
        () => {
          this.$message.success('successfully')
          this.saveBtnLoading = false
          this.$sendLog(903401, {
            bulk_openAutoReply: this.openAutoReply,
            bulk_replyContent: this.replyContent,
            bulk_chooseTimeRange: this.chooseTimeRange,
            bulk_onlyReplyOnce: this.onlyReplyOnce,
            bulk_effectiveTimeRange: effectiveTimeRangeData
          })
        }
      )
    }
  },
  created() {
    chrome.storage.local.get(
      [
        'bulk_openAutoReply',
        'bulk_replyContent',
        'bulk_chooseTimeRange',
        'bulk_onlyReplyOnce',
        'bulk_effectiveTimeRange'
      ],
      (data) => {
        this.openAutoReply = data.bulk_openAutoReply
        this.replyContent = data.bulk_replyContent
        this.chooseTimeRange = data.bulk_chooseTimeRange
        this.onlyReplyOnce = data.bulk_onlyReplyOnce
        if (data.bulk_effectiveTimeRange?.length) {
          this.effectiveStartTime = data.bulk_effectiveTimeRange[0]
          this.effectiveEndTime = data.bulk_effectiveTimeRange[1]
        }
      }
    )
  }
}
</script>

<style scoped>
.auto-reply-box {
  font-size: 14px;
}

.auto-reply-item {
  margin-bottom: 14px;
}

.auto-reply-box >>> .el-switch.is-checked .el-switch__core {
  border-color: #36b64a;
  background-color: #36b64a;
}

.auto-reply-box >>> .el-range-editor.el-input__inner {
  height: 32px;
  padding: 0 10px;
}

.auto-reply-box >>> .el-range-editor.is-active,
.auto-reply-box >>> .el-range-editor.is-active:hover,
.auto-reply-box >>> .el-select .el-input.is-focus .el-input__inner {
  border-color: #36b64a;
  height: 32px;
  padding: 0 10px;
}

.auto-reply-box >>> .el-input.is-active .el-input__inner,
.auto-reply-box >>> .el-input__inner:focus {
  border-color: #36b64a;
}

.auto-reply-box >>> .el-checkbox__inner:hover {
  border-color: #36b64a;
}

.auto-reply-box >>> .el-checkbox__input.is-checked .el-checkbox__inner,
.auto-reply-box >>> .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #36b64a;
  border-color: #36b64a;
}

.auto-reply-box >>> .el-textarea {
  margin-top: 8px;
}

.auto-reply-box >>> .el-textarea__inner:focus {
  border-color: #36b64a;
}

.auto-reply-box >>> .el-textarea__inner {
  min-height: 120px !important;
}

.auto-reply-button {
  text-align: center;
}

.auto-reply-button >>> .el-button--success,
.auto-reply-button >>> .el-button--success:hover {
  background: #36b64a;
  color: #fff;
}
</style>
